import axios from 'axios'

const UTILITY_TYPES_URL_PATH = 'type-utilities'

export default {
  getAll () {
    return axios.get(UTILITY_TYPES_URL_PATH)
  },
  store (params) {
    return axios.post(UTILITY_TYPES_URL_PATH, params)
  },
  update (id, params) {
    return axios.put(UTILITY_TYPES_URL_PATH + `/${id}`, params)
  },
  destroy (id) {
    return axios.delete(UTILITY_TYPES_URL_PATH + `/${id}`)
  }
}
