import axios from 'axios'

const EXPENSES_URL_PATH = '/expenses'

export default {
  getAll () {
    return axios.get(EXPENSES_URL_PATH)
  },
  getByMonthNumber (monthNumber) {
    return axios.get(EXPENSES_URL_PATH + `/month/${monthNumber}`)
  },
  store (params) {
    return axios.post(EXPENSES_URL_PATH, params)
  },
  update (id, params) {
    return axios.put(EXPENSES_URL_PATH + `/${id}`, params)
  },
  destroy (id) {
    return axios.delete(EXPENSES_URL_PATH + `/${id}`)
  }
}
