<template>
  <div class="box box-warning">
    <div class="box-header with-border">
      <h3 class="box-title">{{ t('Debts') }}</h3>
      <div class="box-tools pull-right">
        <span style="font-size: 18px;">+{{ amountDebts }} UAH, -{{ amountYourDebts }} UAH</span>

        <button type="button" class="btn btn-box-tool" data-widget="collapse"><i class="fa fa-minus"></i></button>
        <button type="button" class="btn btn-box-tool" data-widget="remove"><i class="fa fa-times"></i></button>
      </div>
    </div>
    <!-- /.box-header -->
    <div class="box-body">
      <div class="table">
        <!--<div class="table-responsive">-->
        <table class="table no-margin table-hover">
          <thead>
          <tr>
            <th class="col-xs-2">Хто</th>
            <th class="col-xs-2">Кому</th>
            <th class="col-xs-3">Сума</th>
            <th class="col-xs-4">Замiтка</th>
            <th class="col-xs-1"></th>
          </tr>
          </thead>
          <tbody v-if="showDebtsList">
            <Debt
              v-for="(debt, key) in debts"
              :key="key"
              :id="debt.id"
              :is-your="debt.isYour"
              :currency-id="debt.currency"
              :name="debt.name"
              :amount="debt.amount"
              :description="debt.description"
            />
          </tbody>
          <tbody v-else>
          <tr class="text-center">
            <td colspan="5" style="color: rgb(148, 111, 111)">Ще нема боргiв...</td>
          </tr>
          </tbody>
        </table>
      </div>
      <!-- /.table-responsive -->
    </div>
    <!-- /.box-body -->
    <div class="box-footer clearfix">
      <button class="btn btn-sm btn-info btn-flat pull-left" @click="showCreateForm">{{ t('Add New') }}</button>
      <a href="javascript:void(0)" class="btn btn-sm btn-default btn-flat pull-right"
         @click="toggleDisplayListCount"
         v-text="toggleDisplayCountBtnText">
      </a>
      <div class="clearfix"></div>

      <transition name="page" mode="out-in">
        <DebtEditForm
          style="padding-top: 15px;"
          v-if="debtCreateForm.visible"
          @cancel="debtCreateForm.visible = false"
        />
      </transition>
    </div>
    <!-- /.box-footer -->
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import Debt from '@/views/debts/Debt'
import DebtEditForm from '@/views/debts/DebtEditForm'

export default {
  name: 'DebtsTable',
  components: {
    Debt,
    DebtEditForm
  },
  data () {
    return {
      showAll: false,
      debtCreateForm: {
        visible: false
      }
    }
  },
  computed: {
    ...mapGetters({
      debts: 'getDebts',
      amountDebts: 'getAmountDebts',
      amountYourDebts: 'getAmountYourDebts'
    }),
    toggleDisplayCountBtnText () {
      return this.showAll ? this.t('Hide All') : this.t('View All')
    },
    showDebtsList () {
      return this.debts.length > 0
    }
  },
  methods: {
    showCreateForm () {
      this.debtCreateForm.visible = true
    },
    toggleDisplayListCount () {
      this.showAll = !this.showAll
    }
  }
}
</script>
