import Vue from 'vue'
import UtilitiesApi from '@/services/utilities'
import * as types from '../mutation-types'
import { getDateWithoutTime } from '@/utils/helpers'

const state = {
  utility: {
    status: {
      isLoading: false,
      isFailure: false,
      isSaved: false
    },
    amount: 0,
    type: '',
    date: ''
  }
}

const getters = {
  //
}

const actions = {
  saveUtility ({ commit, state }) {
    commit(types.SET_EDIT_UTILITY_LOADING_STATUS)
    let utility = {...state.utility}
    UtilitiesApi.store(utility)
      .then(id => {
        utility['id'] = id
        commit(types.SET_EDIT_UTILITY_SAVED_STATUS)
        commit(types.UPDATE_UTILITIES, utility)
        return true
      })
      .catch(() => {
        commit(types.SET_EDIT_UTILITY_FAILURE_STATUS)
        return false
      })
    commit(types.CLEAR_EDIT_UTILITY_LOADING_STATUS)
  },
  cancelEditUtility ({ commit }) {
    commit(types.CLEAR_EDIT_UTILITY)
  },
  addNewEditUtility ({ commit, getters }, utilityData) {
    const type = utilityData && utilityData.type ? getters.getUtilityTypeById(utilityData.type) : getters.getFirstUtilityType
    const date = utilityData && utilityData.date ? getDateWithoutTime(utilityData.date) : getDateWithoutTime(getters.getUtilitiesDate)
    const amount = utilityData && utilityData.amount ? utilityData.amount : getters.getLastUtilityByType(type.id).amount
    commit(types.SET_EDIT_UTILITY, {
      amount,
      type: type.id,
      date: date.format()
    })
  }
}

const mutations = {
  [types.SET_EDIT_UTILITY] (state, utility) {
    Vue.set(utility, 'status', {
      isLoading: false,
      isSaved: false,
      isFailure: false
    })
    Vue.set(state, 'utility', utility)
    if (utility.id) {
      Vue.set(state, 'utility', {...state.utility, ...{id: utility.id}})
    }
  },
  [types.UPDATE_EDIT_UTILITY] (state, utilityData) {
    Vue.set(state, 'utility', {...state.utility, ...utilityData})
  },
  [types.CLEAR_EDIT_UTILITY] (state) {
    Vue.set(state, 'utility', {})
  },
  [types.SET_EDIT_UTILITY_LOADING_STATUS] (state) {
    state.utility.status.isLoading = true
  },
  [types.SET_EDIT_UTILITY_FAILURE_STATUS] (state) {
    state.utility.status.isFailure = true
  },
  [types.SET_EDIT_UTILITY_SAVED_STATUS] (state) {
    state.utility.status.isLoading = false
    state.utility.status.isFailure = false
    state.utility.status.isSaved = true
  },
  [types.CLEAR_EDIT_UTILITY_LOADING_STATUS] (state) {
    state.utility.status.isLoading = false
  }
}

export default {
  state,
  mutations,
  actions,
  getters
}
