<template>
  <div class="box box-warning">
    <div class="box-header with-border">
      <h3 class="box-title">{{ t('Expense Categories') }}</h3>

      <div class="box-tools pull-right">
        <button type="button" class="btn btn-box-tool" data-widget="collapse"><i class="fa fa-minus"></i></button>
        <button type="button" class="btn btn-box-tool" data-widget="remove"><i class="fa fa-times"></i></button>
      </div>
    </div>
    <!-- /.box-header -->
    <div class="box-body">
      <div class="table">
        <table class="table no-margin table-hover">
          <thead>
          <tr>
            <th class="col-xs-8">{{ t('Category') }}</th>
            <th class="col-xs-3">{{ t('Color') }}</th>
            <th class="col-xs-1"></th>
          </tr>
          </thead>
          <tbody>
          <expense-category
            v-for="category in categories"
            :key="category.id"
            :id="category.id"
            :name="category.name"
            :color="category.color"
          />
          </tbody>
        </table>
      </div>
    </div>
    <!-- /.box-body -->
    <div class="box-footer clearfix">
      <button class="btn btn-sm btn-info btn-flat pull-left" @click="showCreateExpenseCategoryForm">{{ t('Add New') }}</button>
      <a href="javascript:void(0)" class="btn btn-sm btn-default btn-flat pull-right"
         @click="toggleDisplayListCount"
         v-text="toggleDisplayCountBtnText">
      </a>
      <div class="clearfix"></div>

      <transition name="page" mode="out-in">
        <expense-category-edit-form
          style="padding-top: 15px;"
          v-if="editFormVisible"
          @cancel="cancelCreate"
        />
      </transition>
    </div>
    <!-- /.box-footer -->
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import ExpenseCategory from '@/views/expenses/categories/ExpenseCategory'
import ExpenseCategoryEditForm from '@/views/expenses/categories/ExpenseCategoryEditForm'
import * as types from '@/store/mutation-types'

export default {
  name: 'ExpenseCategoriesTable',
  components: {
    ExpenseCategory,
    ExpenseCategoryEditForm
  },
  data () {
    return {
      showAll: false
    }
  },
  computed: {
    ...mapGetters({
      expenseCategories: 'getExpenseCategories'
    }),
    ...mapState({
      editFormVisible: state => state.editExpenseCategory.visible && state.editExpenseCategory.id === ''
    }),
    categories () {
      if (this.showAll) {
        return this.expenseCategories
      }
      return this.expenseCategories.slice(0, 12)
    },
    toggleDisplayCountBtnText () {
      return this.showAll ? this.t('Hide All') : this.t('View All')
    }
  },
  methods: {
    showCreateExpenseCategoryForm () {
      this.$store.commit(types.CLEAR_EXPENSE_CATEGORY)
      this.$store.commit(types.SET_EXPENSE_CATEGORY_VISIBLE, true)
    },
    cancelCreate () {
      this.$store.commit(types.SET_EXPENSE_CATEGORY_VISIBLE, false)
    },
    toggleDisplayListCount () {
      this.showAll = !this.showAll
    }
  }
}
</script>
