<template>
  <div>
    <button class="btn btn-sm btn-info btn-flat pull-left" @click="addMonth">{{ t('Add New') }}</button>
    <!--<Datepicker wrapper-class="date pull-left hidden-xs"-->
                <!--input-class="form-control"-->
                <!--language="uk"-->
                <!--v-model="date"-->
                <!--initial-view="month"-->
    <!--/>-->
  </div>
</template>

<script>
// import Datepicker from 'vuejs-datepicker'
import { getDateWithoutTime } from '@/utils/helpers'
import * as types from '@/store/mutation-types'

export default {
  name: 'AddNewMonth',
  computed: {
    date: {
      get () {
        return this.$store.state.utilities.date
      },
      set (value) {
        this.$store.commit(types.SET_UTILITIES_DATE, value)
      }
    }
  },
  methods: {
    addMonth () {
      this.$store.dispatch('addNewEditUtility').then(() => {
        this.$bus.$emit('activated-month', getDateWithoutTime(this.date).format())
      })
    }
  }
}
</script>

<style scoped>
.date {
  margin-top: 0;
  height: 30px;
}
</style>

<style>
.date input.form-control {
  height: 30px;
  width: 95px;
  background-color: white;
  padding: 3px;
}
</style>