<template>
  <div v-if="!isLoading" class="row">
    <div class="col-md-6">
      <DebtsTable/>
    </div>
  </div>
</template>

<script>
import DebtsTable from '@/views/debts/DebtsTable'

export default {
  name: 'Debts',
  components: {
    DebtsTable
  },
  data () {
    return {
      isLoading: true
    }
  },
  created () {
    Promise
      .all([
        this.$store.dispatch('getDebts'),
        this.$store.dispatch('getCurrencies')
      ])
      .then(() => {
        this.isLoading = false
      })
  }
}
</script>
