import moment from 'moment'

export const generateUUID = function () {
  let d = new Date().getTime()
  return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
    let r = (d + Math.random() * 16) % 16 | 0
    d = Math.floor(d / 16)
    return (c === 'x' ? r : (r & 0x3 | 0x8)).toString(16)
  })
}

export const getDateWithoutTime = function (date) {
  if (!date) {
    date = new Date()
  }
  return moment(date).startOf('d')
  // return moment(date).startOf('d').add(1, 'd')
}

export const getDateWithoutTimeAndDay = function (date) {
  if (!date) {
    date = new Date()
  }
  return moment(date).startOf('month').add(1, 'd')
}

export const compareDate = function (compareA, compareB) {
  compareA = new Date(compareA.date).getTime()
  compareB = new Date(compareB.date).getTime()
  return compareA - compareB
}
export const compareDateDesc = function (compareA, compareB) {
  compareA = new Date(compareA.date).getTime()
  compareB = new Date(compareB.date).getTime()
  return compareB - compareA
}

export const boolCompareDateWithoutTimeAndDay = function (compareDateA, compareDateB) {
  // TODO: test without time and day filter
  compareDateA = moment(compareDateA).startOf('day').add(1, 'd')
  compareDateB = moment(compareDateB).startOf('day').add(1, 'd')
  return compareDateA.isSame(compareDateB)
}

