export const sortByKeyDate = function (datesForSort, desc) {
  // convert to array for sort by date
  let datesForSortInArrayType = []
  for (let dateForSort in datesForSort) {
    datesForSortInArrayType.push({
      date: dateForSort,
      items: datesForSort[dateForSort]
    })
  }
  // sort by date
  if (desc) {
    datesForSortInArrayType.sort(compareDateDesc)
  } else {
    datesForSortInArrayType.sort(compareDate)
  }
  let datesForSortInObjType = {}
  // return array to object grouped by date
  datesForSortInArrayType.forEach((dateForSortInArrayType) => {
    datesForSortInObjType[dateForSortInArrayType.date] = dateForSortInArrayType.items
  })
  console.log(datesForSortInObjType)
  return datesForSortInObjType
}
