<template>
  <tr>
    <td style="padding: 2px;">
      <span>{{ dateCalendar }}</span>
    </td>
    <td>
      <expense
        v-for="item in items"
        :key="item.id"
        :category="getExpenseCategoryById(item.category)"
        :price="item.price"
        :currency="getCurrencyById(item.currency).code"
      />
    </td>
    <td>
      <p class="day-amount">{{ dayAmountInUAH }} &#8372;</p>

      <i class="edit-btn fa fa-pencil" @click="editExpenses()"></i>
    </td>
  </tr>
</template>

<script>
import { mapGetters } from 'vuex'
// import Datepicker from 'vuejs-datepicker'
import Expense from './Expense'
import { boolCompareDateWithoutTimeAndDay } from '@/utils/helpers'

export default {
  name: 'ExpensesByDay',
  components: {
    Expense
  },
  props: {
    date: {
      type: String,
      required: true
    },
    items: {
      type: Array,
      required: true
    },
    editMode: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    ...mapGetters([
      'getExpenseCategoryById',
      'getCurrencyById',
      'getAmountFoodExpensesByDate'
    ]),
    dayAmountInUAH () {
      return this.getAmountFoodExpensesByDate(this.date)['UAH']
    },
    dateCalendar () {
      return this.$moment(this.$moment(this.date)).calendar(null, {
        lastDay: '[Вчера]',
        sameDay: '[Сегодня]',
        nextDay: '[Завтра]',
        lastWeek: 'D dd',
        nextWeek: 'D dd',
        sameElse: 'DD.M'
      })
    }
  },
  methods: {
    editExpenses () {
      this.$store.dispatch('editExpensesByDate', this.date)
      window.scrollTo(0, 0)
    }
  },
  created () {
    this.$bus.$on('show-edit-expenses-form', date => {
      if (boolCompareDateWithoutTimeAndDay(this.date, date)) {
        this.showEditForm = true
      }
    })
  }
}
</script>

<style scoped>
tr td:last-child {
  position: relative;
}

.edit-btn {
  transition: opacity 0.1s linear;
  font-size: 16px;
  position: absolute;
  top: 3px;
  cursor: pointer;
  color: rgb(0, 166, 90);
  right: 8px;
  opacity: 0;
}

tr:hover .edit-btn {
  opacity: 1;
}

.day-amount {
  font-size: 14px;
}

.table > tbody > tr > td, .table > tbody > tr > th, .table > tfoot > tr > td, .table > tfoot > tr > th, .table > thead > tr > td, .table > thead > tr > th {
  padding: 5px;
  line-height: 1.42857143;
  vertical-align: middle;
}
</style>