import Vue from 'vue'
import * as types from '../mutation-types'
import ExpenseCategoryApi from '@/services/expenseCategories'

const state = {
  expenseCategories: []
}

const getters = {
  getExpenseCategories: state => state.expenseCategories,
  getExpenseCategoryById: state => id => state.expenseCategories.find(expenseCategory => expenseCategory.id === id),
  getFirstExpenseCategory: state => state.expenseCategories[0],
  getFoodExpenseCategories: state => state.expenseCategories.filter(item => item.isEdible),
  getNotFoodExpenseCategories: state => state.expenseCategories.filter(item => !item.isEdible)
}

const actions = {
  getExpenseCategories ({ state, commit }) {
    return ExpenseCategoryApi.getAll()
      .then(expenseCategories => {
        commit(types.CLEAR_EXPENSE_CATEGORIES)
        expenseCategories.forEach(item => {
          commit(types.SET_EXPENSE_CATEGORY, item)
        })
      })
      .catch((error) => {
        console.error(error)
      })
  }
}

const mutations = {
  [types.SET_EXPENSE_CATEGORY] (state, expenseCategory) {
    state.expenseCategories.push(expenseCategory)
  },
  [types.UPDATE_EXPENSE_CATEGORY] (state, updatedExpenseCategory) {
    state.expenseCategories.forEach((expenseCategory, index) => {
      if (expenseCategory.id === updatedExpenseCategory.id) {
        Vue.set(state.expenseCategories, index, updatedExpenseCategory)
      }
    })
  },
  [types.DELETE_EXPENSE_CATEGORY] (state, id) {
    state.expenseCategories.forEach((item, index) => {
      if (item.id === id) {
        state.expenseCategories.splice(index, 1)
      }
    })
  },
  [types.CLEAR_EXPENSE_CATEGORIES] (state) {
    state.expenseCategories = []
  }
}

export default {
  state,
  mutations,
  actions,
  getters
}
