<template>
  <div>
    <div class="row">
      <div class="col-xs-1">
        <div class="checkbox">
          <label>
            <input type="checkbox" v-model="isYour">
            Твiй?
          </label>
        </div>
      </div>
      <div class="col-xs-3">
        <input type="text" class="form-control"
               v-model="name"
               placeholder="Введiть iм'я..."
        >
      </div>
      <div class="col-xs-2" style="padding: 0;">
        <div class="input-group details">
          <input type="number"
                 class="form-control amount"
                 placeholder="Борг..."
                 v-model="amount">

          <div class="input-group-addon currency">
            <div class="dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
              <span v-html="selectedCurrencySign"></span>
            </div>
            <ul class="dropdown-menu">
              <li @click="selectCurrency(currency.id)" v-for="currency in currencies">
                <a href="javascript:void(0);">
                  <span v-html="currencySign(currency.id)"></span>
                </a>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <div class="col-xs-4">
        <input type="text" class="form-control"
               v-model="description"
               placeholder="Введiть замiтку..."
        >
      </div>
      <div class="col-xs-2">
        <button class="btn btn-sm btn-info" @click="save"><i class="fa fa-check"></i></button>
        <button class="btn btn-sm" @click="cancel"><i class="fa fa-times"></i></button>
      </div>
    </div>

    <div v-if="status.isSaved">
      Saved
    </div>
    <div v-if="status.isFailure">
      Failure
    </div>
    <div v-if="status.isLoading">
      Loading
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import * as types from '@/store/mutation-types'

export default {
  name: 'DebtEditForm',
  props: {
    id: {
      type: Number
    }
  },
  computed: {
    isYour: {
      get () {
        return this.$store.state.editDebt.isYour
      },
      set (value) {
        this.$store.commit(types.UPDATE_DEBT_IS_YOUR, value)
      }
    },
    name: {
      get () {
        return this.$store.state.editDebt.name
      },
      set (value) {
        this.$store.commit(types.UPDATE_DEBT_NAME, value)
      }
    },
    amount: {
      get () {
        return this.$store.state.editDebt.amount
      },
      set (value) {
        this.$store.commit(types.UPDATE_DEBT_AMOUNT, value)
      }
    },
    description: {
      get () {
        return this.$store.state.editDebt.description
      },
      set (value) {
        this.$store.commit(types.UPDATE_DEBT_DESCRIPTION, value)
      }
    },
    ...mapState({
      currency: state => state.editDebt.currency
    }),
    ...mapState({
      status: state => state.editDebt.status
    }),
    ...mapGetters({
      currencies: 'getCurrencies',
      currencySign: 'getCurrencySignByID'
    }),
    selectedCurrencySign () {
      return this.currencySign(this.currency)
    }
  },
  watch: {
    'status.isSaved': function (value) {
      if (value) {
        setTimeout(() => {
          this.cancel()
        }, 1000)
      }
    }
  },
  methods: {
    save () {
      if (this.id) {
        this.$store.dispatch('updateDebt', this.id)
      } else {
        this.$store.dispatch('addDebt')
      }
    },
    cancel () {
      this.$emit('cancel')
    },
    selectCurrency (currency) {
      this.$store.commit(types.UPDATE_DEBT_CURRENCY, currency)
    }
  },
  mounted () {
    this.$store.dispatch('setEditDebt', this.id)
  }
}
</script>

<style lang="stylus" scoped>
.currency {
  color: rgb(11, 213, 53);
}

.amount {
  text-align: right;
  padding-right: 5px;
}

.input-group-addon {
  position: relative;
  padding: 0;

  .dropdown-toggle {
    padding: 0;
    font-size: 16px;
    width: 23px;
  }

  .dropdown-menu {
    min-width: 0;
    border-radius: 1px;
    left: -1px;
  }

  .dropdown-menu > li > a {
    padding 3px 6px
    color #858585
  }
}
</style>