<template>
  <tr>
    <td v-if="editForm.visible" colspan="5">
      <DebtEditForm
        :id="id"
        @cancel="toggleVisibleEditForm"
      />
    </td>
    <td v-show="!editForm.visible">
      <span v-show="!editForm.visible">{{ who }}</span>
    </td>
    <td v-show="!editForm.visible">
      <span v-show="!editForm.visible">{{ whom }}</span>
    </td>
    <td v-show="!editForm.visible">
      Повернути
      <b>{{ amount }} {{ currency.code }}</b>

      <div class="text-center margin-top-20" v-show="status.tryDelete">
        <h4 style="color: orangered;">Видалити борг?</h4>
        <button class="btn btn-sm btn-info" @click="deleteDebt">Видалити</button>
        <button class="btn btn-sm" @click="cancelDelete">{{ t('Cancel') }}</button>
      </div>
    </td>
    <td v-show="!editForm.visible">
      {{ description }}
    </td>
    <td v-show="!editForm.visible">
      <i class="edit-btn fa fa-pencil" v-show="!editForm.visible" @click="toggleVisibleEditForm"></i>
      <i class="edit-btn delete fa fa-trash" v-show="!editForm.visible" @click="tryDelete"></i>
    </td>
  </tr>
</template>

<script>
import DebtEditForm from '@/views/debts/DebtEditForm'

export default {
  name: 'Debt',
  components: {
    DebtEditForm
  },
  props: {
    id: {
      type: Number,
      required: true
    },
    isYour: {
      type: Boolean,
      required: true
    },
    currencyId: {
      type: Number,
      required: true
    },
    amount: {
      type: Number,
      required: true
    },
    name: {
      type: String,
      required: true
    },
    description: {
      type: String
    }
  },
  data () {
    return {
      status: {
        tryDelete: false
      },
      editForm: {
        visible: false
      }
    }
  },
  computed: {
    currency () {
      return this.$store.getters.getCurrencyById(this.currencyId)
    },
    who () {
      if (this.isYour) {
        return 'Я'
      }
      return this.name
    },
    whom () {
      if (this.isYour) {
        return this.name
      }
      return 'Менi'
    }
  },
  methods: {
    resetStatuses () {
      this.status.tryDelete = false
    },
    tryDelete () {
      this.resetStatuses()
      this.status.tryDelete = true
    },
    toggleVisibleEditForm () {
      this.resetStatuses()
      this.editForm.visible = !this.editForm.visible
    },
    cancelDelete () {
      this.resetStatuses()
    },
    deleteDebt () {
      this.$store.dispatch('deleteDebt', this.id)
        .then(result => {
          if (result) {
            this.resetStatuses()
          }
        })
    }
  },
  created () {
    this.resetStatuses()
  }
}
</script>

<style scoped>
tr td:last-child {
  position: relative;
}

.edit-btn {
  transition: opacity 0.1s linear;
  font-size: 16px;
  position: absolute;
  top: 3px;
  cursor: pointer;
  color: rgb(0, 166, 90);
  right: 25px;
  opacity: 0;
}

.edit-btn.delete {
  color: #dd4b39;
  right: 5px;
}

tr:hover .edit-btn {
  opacity: 1;
}

.margin-top-20 {
  margin-top: 20px;
}
</style>