import Vue from 'vue'
import * as types from '../mutation-types'
import UtilityTypesAPI from '@/services/utilityTypes'
import { generateUUID } from '@/utils/helpers'

const state = {
  utilityTypes: []
}

const getters = {
  getUtilityTypes: state => {
    return state.utilityTypes.filter((item) => {
      return item.status !== 'deleted'
    })
  },
  getFirstUtilityType: state => {
    console.log(state.utilityTypes[0])
    return state.utilityTypes[0]
  },
  getCreatedUtilityTypes: state => {
    return state.utilityTypes.filter((item) => {
      return item.status !== 'deleted'
    })
  },
  getDeletedUtilityTypes: state => {
    return state.utilityTypes.filter((item) => {
      return item.status === 'deleted'
    })
  },
  getUtilityTypeById: state => id => {
    return state.utilityTypes.find(utilityType => utilityType.id === id)
  }
}

const actions = {
  getUtilityTypes ({ commit }) {
    return UtilityTypesAPI.getAll()
      .then((response) => {
        commit(types.SET_UTILITY_TYPES, response.data)
      })
      .catch((error) => {
        console.error(error)
      })
  },
  saveUtilityType ({ commit, state }, uuid) {
    let selectedUtilityType = {}
    state.utilityTypes.forEach((utilityType) => {
      if (utilityType.uuid === uuid) {
        selectedUtilityType = utilityType
      }
    })
    if (selectedUtilityType.status === 'new') {
      UtilityTypesAPI.store(selectedUtilityType)
        .then((response) => {
          commit(types.UPDATE_UTILITY_TYPE, {
            uuid: selectedUtilityType.uuid,
            field: 'id',
            value: response.id
          })
          commit(types.CLEAR_UTILITY_TYPE_STATUS, selectedUtilityType.uuid)
          commit(types.DELETE_ORIGINAL_UTILITY_TYPE, selectedUtilityType.uuid)
        })
    }
    if (selectedUtilityType.status === 'updated') {
      UtilityTypesAPI.update(selectedUtilityType.id, selectedUtilityType)
        .then(() => {
          commit(types.CLEAR_UTILITY_TYPE_STATUS, selectedUtilityType.uuid)
        })
    }
  },
  cancelEditUtilityType ({ commit, state }, uuid) {
    let selectedUtilityType = {}
    state.utilityTypes.forEach((utilityType) => {
      if (utilityType.uuid === uuid) {
        selectedUtilityType = utilityType
      }
    })
    if (selectedUtilityType.status === 'new') {
      commit(types.DELETE_UTILITY_TYPE, selectedUtilityType.uuid)
    }
    if (selectedUtilityType.status === 'updated') {
      commit(types.RETURN_ORIGINAL_UTILITY_TYPE, selectedUtilityType.uuid)
    }
  },
  cancelDeleteUtilityTypes ({ commit, state }) {
    state.utilityTypes.forEach((item) => {
      if (item.status === 'deleted') {
        commit(types.CLEAR_UTILITY_TYPE_STATUS, item.uuid)
      }
    })
  },
  deleteUtilityTypes ({ commit, state }) {
    state.utilityTypes.forEach((item) => {
      if (item.id && item.status === 'deleted') {
        UtilityTypesAPI.delete(item.id)
          .then(() => {
            commit(types.DELETE_UTILITY_TYPE, item.uuid)
          })
          .catch((error) => {
            console.error(error)
          })
      }
    })
  },
  addNewUtilityType ({ commit, getters }) {
    commit(types.SET_UTILITY_TYPE, {
      name: 'New',
      rate: 0,
      currency: getters.getCurrencies[0],
      unit: ''
    })
  }
}

const mutations = {
  // TODO: replace this method on one
  [types.SET_UTILITY_TYPES] (state, utilityTypes) {
    let resultUtilityTypes = []
    utilityTypes.forEach((utilityType) => {
      utilityType.uuid = generateUUID()
      utilityType.status = ''
      resultUtilityTypes.push(utilityType)
    })
    state.utilityTypes = resultUtilityTypes
  },
  [types.SET_UTILITY_TYPE] (state, utilityType) {
    state.utilityTypes.push({
      uuid: generateUUID(),
      status: 'new',
      name: utilityType.name,
      rate: utilityType.rate,
      currency: utilityType.currency,
      unit: utilityType.unit
    })
  },
  [types.UPDATE_UTILITY_TYPE] (state, utilityTypeFieldData) {
    state.utilityTypes.forEach((utilityType, index) => {
      if (utilityType.uuid === utilityTypeFieldData.uuid) {
        let utilityTypeForUpdate = state.utilityTypes[index]
        if (!utilityTypeForUpdate.original) {
          utilityTypeForUpdate.original = Object.assign({}, utilityTypeForUpdate)
        }
        if (utilityTypeForUpdate.status !== 'new') {
          utilityTypeForUpdate.status = 'updated'
        }
        utilityTypeForUpdate[utilityTypeFieldData.field] = utilityTypeFieldData.value
        Vue.set(state.utilityTypes, index, utilityTypeForUpdate)
      }
    })
  },
  [types.DELETE_UTILITY_TYPE] (state, uuid) {
    state.utilityTypes.forEach((item, index) => {
      if (item.uuid === uuid) {
        state.utilityTypes.splice(index, 1)
      }
    })
  },
  [types.ADD_UTILITY_TYPE_STATUS] (state, params) {
    state.utilityTypes.forEach((item, index) => {
      if (item.uuid === params.uuid) {
        let updatedUtilityType = state.utilityTypes[index]
        updatedUtilityType.status = params.status
        Vue.set(state.utilityTypes, index, updatedUtilityType)
      }
    })
  },
  [types.CLEAR_UTILITY_TYPE_STATUS] (state, uuid) {
    state.utilityTypes.forEach((item, index) => {
      if (item.uuid === uuid) {
        let clearStatusUtilityTypes = state.utilityTypes[index]
        clearStatusUtilityTypes.status = ''
        Vue.set(state.utilityTypes, index, clearStatusUtilityTypes)
      }
    })
  },
  [types.RETURN_ORIGINAL_UTILITY_TYPE] (state, uuid) {
    state.utilityTypes.forEach((item, index) => {
      if (item.uuid === uuid && item.original) {
        let originalUtilityType = Object.assign({}, item.original)
        state.utilityTypes.splice(index, 1)
        state.utilityTypes.push(originalUtilityType)
      }
    })
  },
  [types.DELETE_ORIGINAL_UTILITY_TYPE] (state, uuid) {
    state.utilityTypes.forEach((item, index) => {
      if (item.uuid === uuid && item.original) {
        let utilityType = state.utilityTypes[index]
        delete (utilityType.original)
        Vue.set(state.utilityTypes, index, utilityType)
      }
    })
  }
}

export default {
  state,
  mutations,
  actions,
  getters
}
