import Vue from 'vue'
import * as types from '../mutation-types'
import ExpensesApi from '@/services/expenses'
import { getDateWithoutTime, boolCompareDateWithoutTimeAndDay, sortByKeyDate } from '@/utils/helpers'

const state = {
  expenses: [],
  currentMonthNumber: ''
}

const getters = {
  getFoodExpensesGroupedByDate: (state, getters) => {
    let groupedExpenses = {}
    let foodExpenses = getters.getFoodExpenses
    if (foodExpenses) {
      foodExpenses.forEach((item) => {
        let date = item.date
        groupedExpenses[date] = groupedExpenses[date] || []
        groupedExpenses[date].push(item)
      })
      return sortByKeyDate(groupedExpenses, true)
    } else {
      return []
    }
  },
  getAmountExpenses: state => state.expenses.reduce((amount, item) => amount + parseInt(item.price), 0),
  getAmountExpensesByType: (state, getters) => type => {
    let expenses = []
    switch (type) {
      case 'food':
        expenses = getters.getFoodExpenses
        break
      case 'general':
        expenses = getters.getGeneralExpenses
        break
      case 'personal':
        expenses = getters.getPersonalExpenses
        break
      default:
        expenses = []
        break
    }
    if (expenses.length > 0) {
      return expenses.reduce((amount, item) => {
        return amount + parseInt(item.price)
      }, 0)
    } else {
      return 0
    }
  },
  getAmountPersonalExpenses: (state, getters) => {
    let expenses = getters.getGeneralExpenses
    if (expenses) {
      return expenses.reduce((amount, item) => {
        return amount + parseInt(item.price)
      }, 0)
    } else {
      return 0
    }
  },
  getAmountFoodExpensesByDate: (state, getters) => date => {
    let amount = {}
    let foodExpenses = getters.getFoodExpenses
    if (foodExpenses.length > 0) {
      foodExpenses.forEach(item => {
        let currency = getters.getCurrencyById(item.currency)
        if (boolCompareDateWithoutTimeAndDay(date, item.date)) {
          if (!amount[currency.code]) {
            amount[currency.code] = 0
          }
          amount[currency.code] += parseInt(item.price)
        }
      })
    }
    return amount
  },
  getExpensesByDate: state => date => state.expenses.filter(expense => expense.date === date),
  getLastExpensesByCount: state => count => state.expenses.slice(0 - count).reverse(),
  getFoodExpenses: (state, getters) => state.expenses.filter(expense => getters.getFoodExpenseCategories.find(category => expense.category === category.id) && expense.isGeneral),
  getGeneralExpenses: (state, getters) => state.expenses.filter(expense => getters.getNotFoodExpenseCategories.find(category => expense.category === category.id) && expense.isGeneral),
  getPersonalExpenses: state => state.expenses.filter(expense => !expense.isGeneral),
  getExpenseById: state => id => state.expenses.find(expense => expense.id === id)
}

const actions = {
  getAllExpenses ({ commit }) {
    return ExpensesApi.getAll()
      .then((response) => {
        commit(types.CLEAR_EXPENSES)
        response.data.forEach((expense) => {
          commit(types.SET_EXPENSE, expense)
        })
      })
      .catch((error) => {
        console.error(error)
      })
  },
  getExpensesByMonthNumber ({ state, commit }, monthNumber) {
    if (state.currentMonthNumber !== monthNumber || !state.expenses || Object.keys(state.expenses).length === 0) {
      commit(types.SET_CURRENT_MONTH_NUMBER, monthNumber)
      return ExpensesApi.getByMonthNumber(monthNumber)
        .then((response) => {
          commit(types.CLEAR_EXPENSES)
          response.forEach(item => {
            commit(types.SET_EXPENSE, item)
          })
        })
        .catch((error) => {
          console.error(error)
        })
    }
  },
  editExpensesByDate ({ state, commit, getters }, date) {
    commit(types.CLEAR_EDIT_EXPENSES)
    commit(types.SET_EDIT_EXPENSE_DATE, date)
    getters.getExpensesByDate(date).forEach(expense => commit(types.SET_EDIT_EXPENSE, expense))
  }
}

const mutations = {
  [types.SET_CURRENT_MONTH_NUMBER] (state, number) {
    state.currentMonthNumber = number
  },
  [types.SET_EXPENSE] (state, expense) {
    expense.date = getDateWithoutTime(expense.date).format()
    state.expenses.push(expense)
  },
  [types.UPDATE_EXPENSE] (state, expenseForUpdate) {
    state.expenses.forEach((expense, index) => {
      if (expenseForUpdate.id === expense.id) {
        Vue.set(state.expenses, index, expenseForUpdate)
      }
    })
  },
  [types.DELETE_EXPENSE] (state, id) {
    state.expenses.forEach((item, index) => {
      if (item.id === id) {
        state.expenses.splice(index, 1)
      }
    })
  },
  [types.CLEAR_EXPENSES] (state) {
    state.expenses = []
  }
}

export default {
  state,
  mutations,
  actions,
  getters
}
