<template>
  <el-autocomplete
    ref="categoryAutocomplete"
    class="inline-input categories-autocomplete"
    v-model="category.name"
    :fetch-suggestions="querySearch"
    :placeholder="t('Category')"
    @select="handleSelect"
    value-key="name"
    popper-class="categories-autocomplete-dropdown"
    auto-correct="off"
  >
    <div class="clear-category" slot="suffix" @click="clear()">
      <i class="fa fa-times"></i>
    </div>
    <template slot-scope="{ item }">
      <i class="ion ion-pizza"></i>
      <span class="link">{{ item.name }}</span>
    </template>
  </el-autocomplete>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'CategoryAutocomplete',
  props: {
    categoryId: Number
  },
  data () {
    return {
      category: {
        name: ''
      }
    }
  },
  computed: {
    ...mapGetters({
      expenseCategories: 'getExpenseCategories'
    })
  },
  methods: {
    querySearch (queryString, cb) {
      let categories = this.expenseCategories
      let results = queryString ? categories.filter((category) => {
        return category.name.toLowerCase().trim().indexOf(queryString.toLowerCase().trim()) === 0
      }) : categories
      cb(results)
    },
    handleSelect (item) {
      this.$emit('select', item.id)
    },
    clear () {
      this.category.name = ''
      setTimeout(() => {
        this.$refs.categoryAutocomplete.focus()
      }, 300)
      this.$emit('clear')
    }
  },
  mounted () {
    if (this.categoryId) {
      this.category.name = this.$store.getters.getExpenseCategoryById(this.categoryId).name
    }
  }
}
</script>

<style lang="stylus" scoped>
.clear-category {
  font-size: 16px;
  padding: 6px 0;
  cursor: pointer;
}
</style>
