<template>
  <div @click="selectDate">
    <div class="box-month" :style="{ 'background-color': monthProperty.color }" :class="{ active: active }">
      <div class="title">{{ monthProperty.name }} <i class="fa fa-check" v-show="active"></i></div>
      <hr>
      <div class="row">
        <div class="col-xs-5 text-center padding-h-0">
          <i class="month-icon" :class="monthProperty.icon" :style="{ color: monthProperty.iconColor }"></i>
          <br>
          <hr>
          <span>{{ monthTotalAmount }} грн</span>
        </div>
        <div class="col-xs-7 padding-h-0">
          <Utility v-for="utilityType in utilityTypes"
                   :key="utilityType.id"
                   :id="utilityType.id"
                   :name="utilityType.name"
                   :unit="utilityType.unit"
                   :date="date.format()"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { months, getDateWithoutTimeAndDay, boolCompareDateWithoutTimeAndDay } from '@/utils/helpers'
import UtilityForText from '@/views/utilities/UtilityForText'
import * as types from '@/store/mutation-types'

export default {
  name: 'MonthUtility',
  components: {
    Utility: UtilityForText
  },
  props: {
    initDate: {
      type: String,
      required: true
    }
  },
  computed: {
    ...mapGetters({
      utilityTypes: 'getCreatedUtilityTypes',
      selectedDate: 'getUtilitiesDate'
    }),
    date () {
      return getDateWithoutTimeAndDay(this.initDate)
    },
    monthNumber () {
      return this.date.month()
    },
    monthProperty () {
      return months[this.monthNumber]
    },
    active () {
      return boolCompareDateWithoutTimeAndDay(this.date, this.selectedDate)
    },
    monthTotalAmount () {
      return this.$store.getters.getMonthUtilitiesTotalByDate(this.date)
    }
  },
  methods: {
    selectDate () {
      this.$store.commit(types.SET_UTILITIES_DATE, this.date)
    }
  }
}
</script>

