<template>
  <div class="info-box" :style="`background-color: ${ backgroundColor }; color: ${ color };`">
    <span class="info-box-icon"><i :class="`ion ${ icon }`"></i></span>
    <div class="info-box-content">
      <span class="info-box-text">Сума витрат на {{ t(type) }}</span>
      <span class="info-box-number">{{ amount }} UAH</span>
      <div class="progress">
        <div class="progress-bar" :style="{ width: leftMoneyInPercent }"></div>
      </div>
      <span class="progress-description">
        {{ leftMoneyInPercent }} витрачено из {{ limit }} грн
      </span>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'amountExpenses',
    props: {
      icon: {
        type: String
      },
      limit: {
        type: [Number, String],
        required: true
      },
      type: {
        type: [Number, String],
        required: true
      },
      color: {
        type: String
      },
      backgroundColor: {
        type: String
      }
    },
    computed: {
      amount () {
        return this.$store.getters.getAmountExpensesByType(this.type)
      },
      leftMoneyInPercent () {
        return Math.round(this.amount / (this.limit / 100)) + '%'
      }
    },
    created () {
      // TODO: разобраться с множественным вызовом расходов по месяцу
      // let currentMonthNumber = (new Date()).getMonth() + 1
      // this.$store.dispatch('getExpensesByMonthNumber', currentMonthNumber).then(() => {
      // this.$bus.$emit('stop-loading')
      // })
    }
  }
</script>