import Vue from 'vue'
import moment from 'moment'
import UtilitiesApi from '@/services/utilities'
import * as types from '../mutation-types'
import { getDateWithoutTime, getDateWithoutTimeAndDay, boolCompareDateWithoutTimeAndDay, sortByKeyDate } from '@/utils/helpers'

const state = {
  utilities: [],
  date: getDateWithoutTime(moment()).format()
}

const getters = {
  getUtilitiesDate: state => state.date,
  getGroupedUtilities: state => {
    // group by date
    let groupedUtilities = state.utilities.reduce(function (groups, item) {
      let date = getDateWithoutTimeAndDay(item['date']).format()
      groups[date] = groups[date] || []
      groups[date].push(item)
      return groups
    }, {})
    return sortByKeyDate(groupedUtilities)
  },
  getUtilityByTypeAndDate: state => params => state.utilities.find(utility => boolCompareDateWithoutTimeAndDay(utility.date, params.date) && (utility.type === params.type)),
  getPreviousUtilityByTypeAndDate: (state, getters) => params => {
    return getters.getUtilityByTypeAndDate({
      type: params.type,
      date: moment(params.date).subtract(1, 'M')
    })
  },
  getLastUtilityByType: state => type => {
    let lastUtility
    state.utilities.forEach(utility => {
      if (utility.type === type) {
        if (!lastUtility) {
          lastUtility = utility
        } else {
          if (moment(utility.date).isAfter(moment(lastUtility.date))) {
            lastUtility = utility
          }
        }
      }
    })
    return lastUtility
  },
  getPreviousLastUtilityByType: (state, getters) => type => {
    let lastUtility = getters.getLastUtilityByType(type)
    let previousLastUtility
    state.utilities.forEach(utility => {
      if (utility.type === type && moment(utility.date).isBefore(lastUtility.date)) {
        if (!previousLastUtility) {
          previousLastUtility = utility
        } else {
          if (moment(utility.date).isAfter(moment(previousLastUtility.date))) {
            previousLastUtility = utility
          }
        }
      }
    })
    return previousLastUtility
  },
  getMonthUtilitiesTotalByDate: (state, getters) => searchDate => {
    let amount = 0
    state.utilities.find(item => {
      if (boolCompareDateWithoutTimeAndDay(item.date, searchDate)) {
        let type = getters.getUtilityTypeById(item.type)
        let rateRules = getters.getUtilityRateRuleByIds(type.rateRules)
        let itemAmount = getters.getUtilityAmountByTypeAndDate({
          'type': type.id,
          'date': item.date
        })
        rateRules.forEach(rateRule => {
          if (itemAmount <= rateRule.limit || !rateRule.limit) {
            amount += itemAmount * rateRule.rate
          }
          if (itemAmount > rateRule.limit) {
            amount += rateRule.limit * rateRule.rate
            itemAmount -= rateRule.limit
          }
        })
        amount = Math.round(amount)
      }
    })
    return amount
  },
  getMonthUtilitiesTotalByTypeAndDate: (state, getters) => params => {
    const type = getters.getUtilityTypeById(params.type)
    const date = params.date
    let amount = 0
    let utilityAmount = getters.getUtilityAmountByTypeAndDate({
      'type': type.id,
      'date': date
    })
    const rateRules = getters.getUtilityRateRuleByIds(type.rateRules)
    rateRules.forEach(rateRule => {
      if (utilityAmount <= rateRule.limit || !rateRule.limit) {
        amount += utilityAmount * rateRule.rate
      }
      if (utilityAmount > rateRule.limit) {
        amount += rateRule.limit * rateRule.rate
        utilityAmount -= rateRule.limit
      }
    })
    return Math.round(amount)
  },
  getUtilityAmountByTypeAndDate: (state, getters) => params => {
    let amount = 0
    let type = params.type
    let date = params.date
    let utility = getters.getUtilityByTypeAndDate({
      type,
      date
    })
    let previousUtility = getters.getPreviousUtilityByTypeAndDate({
      type,
      date
    })
    let utilityAmount = (utility && utility.amount) ? utility.amount : 0
    let previousUtilityAmount = (previousUtility && previousUtility.amount) ? previousUtility.amount : 0
    if (utilityAmount && previousUtilityAmount) {
      amount = utilityAmount - previousUtilityAmount
    }
    return amount
  },
  getLastUtilityAmountByType: (state, getters) => type => {
    let amount = 0
    let utility = getters.getLastUtilityByType(type)
    let previousUtility = getters.getPreviousLastUtilityByType(type)
    let utilityAmount = (utility && utility.amount) ? utility.amount : 0
    let previousUtilityAmount = (previousUtility && previousUtility.amount) ? previousUtility.amount : 0
    if (utilityAmount && previousUtilityAmount) {
      amount = utilityAmount - previousUtilityAmount
    }
    return amount
  },
  getAverageUtilityAmountByTypeAndDate: (state, getters) => params => {
    let type = params.type
    let date = params.date
    let amount = getters.getUtilityAmountByTypeAndDate({
      type,
      date
    })
    let utility = getters.getUtilityByTypeAndDate({
      type,
      date
    })
    if (amount > 0) {
      let daysInMonth = moment(date).daysInMonth()
      let days = moment().diff(moment(utility.date), 'days')
      amount = Math.round((amount / days) * daysInMonth)
    }
    return amount
  }
}

const actions = {
  getUtilities ({ commit }) {
    return UtilitiesApi.getAll()
      .then(utilities => {
        utilities.forEach(utility => commit(types.SET_UTILITY, utility))
      })
      .catch((error) => {
        console.error(error)
      })
  }
}

const mutations = {
  [types.SET_UTILITY] (state, utilityData) {
    state.utilities.push({
      id: utilityData.id,
      amount: utilityData.amount,
      date: getDateWithoutTime(utilityData.date).format(),
      type: utilityData.type
    })
  },
  [types.UPDATE_UTILITIES] (state, utilityData) {
    const utilityIndex = state.utilities.findIndex(utility => utility.id === utilityData.id)
    const utilityForUpdate = {
      id: utilityData.id,
      amount: utilityData.amount,
      date: getDateWithoutTimeAndDay(utilityData.date).format(),
      type: utilityData.type
    }
    if (utilityIndex) {
      Vue.set(state.utilities, utilityIndex, utilityForUpdate)
    } else {
      state.utilities.push(utilityForUpdate)
    }
  },
  [types.SET_UTILITIES_DATE] (state, date) {
    state.date = getDateWithoutTime(date).format()
  }
}

export default {
  state,
  mutations,
  actions,
  getters
}
