<template>
  <div class="row">
    <div class="col-xs-12 col-sm-9">
      <UtilitiesTable/>
    </div>
    <div class="col-xs-12 col-sm-3">
      <UtilityTypesTable/>
    </div>
  </div>
</template>

<script>
import UtilitiesTable from '@/views/utilities/UtilitiesTable'
import UtilityTypesTable from '@/views/utilities/types/UtilityTypesTable'

export default {
  name: 'Utilities',
  components: {
    UtilitiesTable,
    UtilityTypesTable
  },
  data () {
    return {
      isLoaded: false
    }
  },
  created () {
    Promise
      .all([
        this.$store.dispatch('getCurrencies'),
        this.$store.dispatch('getUtilities'),
        this.$store.dispatch('getUtilityTypes'),
        this.$store.dispatch('getUtilityRateRules')
      ])
      .then(() => {
        this.isLoaded = true
      })
  }
}
</script>