<template>
  <div class="box box-success">
    <div class="box-header with-border">
      <h3 class="box-title">Постачальники</h3>

      <div class="box-tools pull-right">
        <button type="button" class="btn btn-box-tool" data-widget="collapse"><i class="fa fa-minus"></i></button>
        <button type="button" class="btn btn-box-tool" data-widget="remove"><i class="fa fa-times"></i></button>
      </div>
    </div>
    <!-- /.box-header -->
    <div class="box-body">
      <div class="table">
        <!--<div class="table-responsive">-->
        <table class="table no-margin table-hover">
          <thead>
          <tr>
            <th class="col-xs-5">{{ t('name') }}</th>
            <th class="col-xs-1">{{ t('unit') }}</th>
            <th class="col-xs-4">{{ t('rateRules') }}</th>
            <th class="col-xs-1">{{ t('currency') }}</th>
            <th class="col-xs-1"></th>
          </tr>
          </thead>
          <tbody>
          <utility-type
            v-for="(utilityType, key) in utilityTypes"
            :key="key"
            :uuid="utilityType.uuid"
            :name="utilityType.name"
            :rate-rule-ids="utilityType.rateRules"
            :currency-id="utilityType.currency"
            :unit="utilityType.unit"
          ></utility-type>
          </tbody>
        </table>
      </div>
      <!-- /.table-responsive -->
    </div>
    <!-- /.box-body -->
    <div class="box-footer clearfix">
      <button class="btn btn-sm btn-info btn-flat pull-left" @click="addUtilityType">Add New</button>

      <a href="javascript:void(0)" class="btn btn-sm btn-default btn-flat pull-right" v-show="deletedUtilityTypes.length === 0">View All</a>

      <button class="btn btn-sm btn-success pull-right" @click="cancelDelete" v-show="deletedUtilityTypes.length > 0">Cancel Delete {{ deletedUtilityTypes.length }} items</button>
      <button class="btn btn-sm btn-danger pull-right" style="margin-right: 5px;" @click="deleteTypes" v-show="deletedUtilityTypes.length > 0">Delete</button>

      <div class="clearfix"></div>
    </div>
    <!-- /.box-footer -->
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import UtilityType from '@/views/utilities/types/UtilityType'

export default {
  name: 'UnityTypesTable',
  components: {
    UtilityType
  },
  computed: {
    ...mapGetters({
      utilityTypes: 'getUtilityTypes',
      deletedUtilityTypes: 'getDeletedUtilityTypes'
    })
  },
  methods: {
    addUtilityType () {
      this.$store.dispatch('addNewUtilityType')
      this.$nextTick(function () {
        let lastUtilityType = this.utilityTypes[this.utilityTypes.length - 1]
        this.$bus.$emit('show-edit-utility-type-form', lastUtilityType.uuid)
      })
    },
    cancelDelete () {
      this.$store.dispatch('cancelDeleteUtilityTypes')
    },
    deleteTypes () {
      this.$store.dispatch('deleteUtilityTypes')
    }
  }
}
</script>