"use strict";

import Vue from 'vue';
import lodash from 'lodash';

Plugin.install = function(Vue, options) {
  Vue._ = lodash;
  window._ = lodash;
  Object.defineProperties(Vue.prototype, {
    _: {
      get() {
        return lodash;
      }
    },
    $lodash: {
      get() {
        return lodash;
      }
    },
  });
};

Vue.use(Plugin)

export default Plugin;
