<template>
  <div>
    <div v-shortkey="['ctrl', 'alt']" @shortkey="addExpense"></div>

    <div class="text-center" style="cursor: pointer;"
         v-if="expenses.length === 0"
         @click="addExpense"
    >
      <p style="color: rgb(148, 111, 111); font-size: 16px;">Що нового сьогодні?</p>
    </div>

    <transition name="page" mode="out-in">
      <div v-if="expenses.length > 0 && !isAllSaved">
        <div v-shortkey="['shift', 'enter']" @shortkey="saveExpenses"></div>
        <div v-shortkey="['esc']" @shortkey="cancelEditExpenses"></div>
        <div class="row" style="margin: 0;">
          <div class="col-sm-2">
            <!--<Datepicker-->
              <!--wrapper-class="date"-->
              <!--input-class="form-control"-->
              <!--language="uk"-->
              <!--v-model="date"-->
            <!--/>-->
          </div>
          <div class="col-sm-10 expenses">
            <div class="col-md-4" style="padding: 3px;"
                 v-for="expense in expenses"
            >
              <ExpenseEdit
                :key="expense"
                :uuid="expense"
              />
            </div>
          </div>
        </div>
        <div class="text-center margin-top-10">
          <span class="add-expense-btn" @click="addExpense(true)"><i class="fa fa-plus"></i> {{ t('Add expense') }}</span>
        </div>
        <div class="text-center margin-top-20" v-if="expenses.length > 0">
          <button class="btn btn-sm btn-info" @click="saveExpenses">{{ t('Save') }}</button>
          <button class="btn btn-sm" @click="cancelEditExpenses">{{ t('Cancel') }}</button>
        </div>
      </div>
    </transition>

    <div v-show="isAllSaved">
      <div class="text-center center" style="color: #65bd65;">
        <i class="fa fa-check" style="font-size: 48px"></i>
        <p style="font-size: 24px;">{{ t('Loaded succeed') }}:)</p>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import * as types from '@/store/mutation-types'
import ExpenseEdit from './ExpenseEdit'
import { getDateWithoutTime } from '@/utils/helpers'
// import Datepicker from 'vuejs-datepicker'

export default {
  name: 'ExpensesEditForm',
  components: {
    ExpenseEdit
  },
  data () {
    return {
      countUpdated: 0
    }
  },
  computed: {
    ...mapState({
      expenses: state => state.editExpenses.expenseList
    }),
    ...mapGetters({
      isAllSaved: 'isAllSavedEditExpenses'
    }),
    date: {
      get () {
        return this.$store.state.editExpenses.date
      },
      set (value) {
        this.$store.dispatch('updateEditExpenseDate', value)
      }
    },
    dateString () {
      return getDateWithoutTime(this.date).format()
    }
  },
  watch: {
    isAllSaved: function (newExpenses, oldExpenses) {
      if (newExpenses && newExpenses !== oldExpenses) {
        this.clear()
      }
    }
  },
  methods: {
    addExpense () {
      this.$store.dispatch('addNewEditExpense', true)
    },
    cancelEditExpenses () {
      this.$store.commit(types.CLEAR_EDIT_EXPENSES)
    },
    clear () {
      setTimeout(() => {
        this.cancelEditExpenses()
      }, 2000)
    },
    saveExpenses () {
      this.$store.dispatch('saveEditExpenses')
    }
  }
}
</script>

<style scoped>
.margin-top-10 {
  margin-top: 10px;
}

.margin-top-20 {
  margin-top: 20px;
}

@media (max-width: 768px) {
  .expenses {
    padding: 0;
  }
}

@media (min-width: 768px) {
  .expenses {
    padding: 0 5px 0 0;
  }
}

.add-expense-btn {
  cursor: pointer;
  color: #6f6f6f;
}

.add-expense-btn .fa {
  color: rgb(0, 166, 90);
}
</style>