import axios from 'axios'

const CATEGORY_EXPENSE_URL_PATH = '/category-expenses'

export default {
  getAll () {
    return axios.get(CATEGORY_EXPENSE_URL_PATH)
  },
  store (params) {
    return axios.post(CATEGORY_EXPENSE_URL_PATH, params)
  },
  update (id, params) {
    return axios.put(CATEGORY_EXPENSE_URL_PATH + `/${id}`, params)
  },
  destroy (id) {
    return axios.delete(CATEGORY_EXPENSE_URL_PATH + `/${id}`)
  }
}
