<template>
  <div>
    <v-card-title primary-title style="justify-content: center">
      <h3 class="headline">LOG IN</h3>
    </v-card-title>
    <v-card-text>
      <v-form>
        <v-text-field prepend-icon="person" name="login" label="Login" autocomplete="username" type="text" v-model="email"></v-text-field>
        <v-text-field id="password" prepend-icon="lock" name="password" label="Password" autocomplete="current-password" type="password" v-model="password"></v-text-field>
        <v-layout align-center>
          <v-switch
            label="Remember"
            color="indigo"
          ></v-switch>
          <v-spacer></v-spacer>
          <span class="indigo--text text--lighten-1">Forgot password?</span>
        </v-layout>
      </v-form>
    </v-card-text>
    <v-card-actions>
      <v-layout align-center justify-center>
        <v-btn color="primary" round block @click="login()">Login</v-btn>
      </v-layout>
    </v-card-actions>
  </div>
</template>

<script>
export default {
  name: 'Login',
  data() {
    return {
      email: '',
      password: '',
      loading: false
    }
  },
  methods: {
    login () {
      this.loading = true
      this.$store.dispatch('loginUser', {
        email: this.email,
        password: this.password
      }).then(() => {
        this.loading = false
        this.$router.push({ name: 'Dashboard' })
        this.$store.dispatch('fetchRoles')
        this.$store.dispatch('fetchUser')
      })
    }
  }
}
</script>

