<template>
  <div id="register">
    <p class="login-box-msg">Register a new membership</p>
    <form v-on:submit.prevent="handleFormSubmit">
      <div class="form-group has-feedback">
        <input type="text" class="form-control" placeholder="Full name" v-model="credentials.name">
        <span class="glyphicon glyphicon-user form-control-feedback"></span>
      </div>
      <div class="form-group has-feedback">
        <input type="email" class="form-control" placeholder="Email" v-model="credentials.email">
        <span class="glyphicon glyphicon-envelope form-control-feedback"></span>
      </div>
      <div class="form-group has-feedback">
        <input type="password" class="form-control" placeholder="Password" v-model="credentials.password">
        <span class="glyphicon glyphicon-lock form-control-feedback"></span>
      </div>
      <div class="form-group has-feedback">
        <input type="password" class="form-control" placeholder="Retype password" v-model="credentials.retypePassword">
        <span class="glyphicon glyphicon-log-in form-control-feedback"></span>
      </div>
      <div class="row">
        <div class="col-xs-6">
          <div class="checkbox icheck">
            <label>
              <div class="icheckbox_square-blue" aria-checked="false" aria-disabled="false" style="position: relative;"><input type="checkbox" style="position: absolute; top: -20%; left: -20%; display: block; width: 140%; height: 140%; margin: 0px; padding: 0px; background: rgb(255, 255, 255); border: 0px; opacity: 0;"><ins class="iCheck-helper" style="position: absolute; top: -20%; left: -20%; display: block; width: 140%; height: 140%; margin: 0px; padding: 0px; background: rgb(255, 255, 255); border: 0px; opacity: 0;"></ins></div> I agree to the <a href="#">terms</a>
            </label>
          </div>
        </div>
        <!-- /.col -->
        <div class="col-xs-6">
          <button type="submit" class="btn btn-light btn-block btn-flat"><loader-circle size="16" v-if="loading" /> Register</button>
        </div>
        <!-- /.col -->
      </div>
    </form>
    <div class="social-auth-links text-center">
      <p>- OR -</p>
      <a href="#" class="btn btn-block btn-social btn-facebook btn-flat"><i class="fa fa-facebook"></i> Sign up using Facebook</a>
      <a href="#" class="btn btn-block btn-social btn-google btn-flat"><i class="fa fa-google-plus"></i> Sign up using Google+</a>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
import LoaderCircle from '@/components/LoaderCircle'

export default {
  name: 'Register',
  components: {
    LoaderCircle
  },
  data () {
    return {
      credentials: {
        name: '',
        email: '',
        password: '',
        retypePassword: ''
      },
      loading: false
    }
  },
  methods: {
    ...mapActions([
      'register',
      'getUser'
    ]),
    handleFormSubmit () {
      if (this.credentials.password === this.credentials.retypePassword) {
        this.loading = true
        this.register(this.credentials)
          .then((response) => {
            this.loading = false
            if (response) {
              this.getUser()
              this.$router.push({name: 'Dashboard'})
            }
          })
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
#register {
  button, a {
    color #ffffff
  }
}

.btn-light {
  background-color #3fbcf3
  boder-color #3db0e6
  color #ffffff
  font-weight bold

  &:hover {
    background-color #41c6ff
  }
}
</style>