import axios from 'axios'

const DEBT_URL_PATH = '/debts'

export default {
  getAll () {
    return axios.get(DEBT_URL_PATH)
  },
  store (params) {
    return axios.post(DEBT_URL_PATH, params)
  },
  update (id, params) {
    return axios.put(DEBT_URL_PATH + `/${id}`, params)
  },
  destroy (id) {
    return axios.delete(DEBT_URL_PATH + `/${id}`)
  }
}
