<template>
  <tr>
    <td>{{ name }}</td>
    <td>{{ amount }} <span v-show="showAverage">(~{{ averageAmount }})</span>{{ unit }}</td>
    <td class="relative hidden-xs" :class="{ 'active-edit': showEditForm}">
      <span v-show="!showEditForm">{{ indications }}</span>
      <div class="input-group" style="width: 1px;" v-show="showEditForm">
        <input type="text" class="form-control indications"
               v-model="editIndication"
        >
        <div class="input-group-addon save-btn" @click="saveUtility()">
          <i class="fa fa-check"></i>
        </div>
        <div class="input-group-addon cancel-btn" @click="cancelEditUtility()">
          <i class="fa fa-times"></i>
        </div>
      </div>

      <i class="edit-btn fa fa-pencil" v-show="!showEditForm" @click="edit()"></i>
    </td>
    <td>{{ total }} <span v-show="showAverage">(~{{ averageTotal }})</span></td>
    <td class="hidden-xs">
      <span>{{ currency.code }}</span>
    </td>
  </tr>
</template>

<script>
import { mapState } from 'vuex'
import * as types from '@/store/mutation-types'
import { boolCompareDateWithoutTimeAndDay, getDateWithoutTimeAndDay } from '@/utils/helpers'

export default {
  name: 'UtilityForTable',
  props: {
    id: {
      type: Number,
      required: true
    },
    name: {
      type: String,
      required: true
    },
    unit: {
      type: String,
      required: true
    },
    rateRuleIds: {
      type: Array,
      required: true
    },
    currencyId: {
      type: Number
    }
  },
  data () {
    return {
      showEditForm: false,
      showAverage: false
    }
  },
  computed: {
    ...mapState({
      date: state => state.utilities.date
    }),
    editIndication: {
      get () {
        if (this.$store.state.editUtility.utility.amount) {
          let amount = this.$store.state.editUtility.utility.amount.toString()
          return '000000'.slice(amount.length) + amount
        }
        return '000000'
      },
      set (value) {
        this.$store.commit(types.UPDATE_EDIT_UTILITY, {
          amount: parseInt(value)
        })
      }
    },
    rateRules () {
      return this.$store.getters.getUtilityRateRuleByIds(this.rateRuleIds)
    },
    utility () {
      let utiltity = this.$store.getters.getUtilityByTypeAndDate({
        'type': this.id,
        'date': this.date
      })
      if (!utiltity) {
        utiltity = this.$store.getters.getLastUtilityByType(this.id)
      }
      return utiltity
    },
    amount () {
      let utilityAmount = 0
      utilityAmount = this.$store.getters.getUtilityAmountByTypeAndDate({
        'type': this.id,
        'date': this.date
      })
      if (utilityAmount === 0) {
        utilityAmount = this.$store.getters.getLastUtilityAmountByType(this.id)
      }
      return utilityAmount
    },
    averageAmount () {
      let amount = this.$store.getters.getAverageUtilityAmountByTypeAndDate({
        'type': this.id,
        'date': this.date
      })
      this.showAverage = boolCompareDateWithoutTimeAndDay(this.date, getDateWithoutTimeAndDay().format()) && amount > 0
      return amount
    },
    currency () {
      return this.$store.getters.getCurrencyById(this.currencyId)
    },
    total () {
      return this.$store.getters.getMonthUtilitiesTotalByTypeAndDate({
        type: this.id,
        date: this.date
      })
    },
    averageTotal () {
      let total = 0
      let amount = this.averageAmount
      this.rateRules.forEach(rateRule => {
        if (amount <= rateRule.limit || !rateRule.limit) {
          total += amount * rateRule.rate
        }
        if (amount > rateRule.limit) {
          total += rateRule.limit * rateRule.rate
          amount -= rateRule.limit
        }
      })
      return Math.round(total)
    },
    indications () {
      let utilityAmount = ''
      if (this.utility && this.utility.amount) {
        utilityAmount = this.utility.amount.toString()
      }
      return '000000'.slice(utilityAmount.length) + utilityAmount
    }
  },
  methods: {
    edit () {
      this.showEditForm = true
      this.$store.dispatch('addNewEditUtility', {
        type: this.id
      })
    },
    saveUtility () {
      this.$store.dispatch('saveUtility', this.id)
        .then(() => {
          this.showEditForm = false
        })
    },
    cancelEditUtility () {
      this.$store.commit(types.CLEAR_EDIT_UTILITY)
      this.showEditForm = false
    }
  },
  created () {
    this.$bus.$on('show-edit-form-new-utility', (date) => {
      if (date === this.date && this.utility && this.utility.status.indexOf('new') !== -1) {
        this.edit()
      }
    })
  }
}
</script>

<style lang="stylus" scoped>
.relative {
  position: relative;
}

.save-btn {
  background-color #00c0ef
  color #ffffff
  border 1px
  border-left none
  cursor pointer
}

.cancel-btn {
  background-color #f75d3c
  cursor pointer
  border 1px
  color #ffffff
}

.edit-btn {
  transition: opacity 0.1s linear;
  font-size: 16px;
  position: absolute;
  top: 3px;
  cursor: pointer;
  color: rgb(0, 166, 90);
  left: 60px;
  opacity: 0;
}

tr:hover .edit-btn {
  opacity: 1;
}

.margin-top-10 {
  margin-top: 10px;
}

.indications {
  text-align: right;
  width: 80px;
}

.btn {
  padding: 5px;
}

.active-edit {
  padding-top 3px
  padding-bottom 3px
}
</style>