<template>
  <div class="box box-info food-expenses">
    <div class="box-header with-border">
      <h3 class="box-title">{{ t('Food') }}</h3>

      <div class="box-tools pull-right">
        <button type="button" class="btn btn-box-tool" data-widget="collapse"><i class="fa fa-minus"></i></button>
        <button type="button" class="btn btn-box-tool" data-widget="remove"><i class="fa fa-times"></i></button>
      </div>
    </div>
    <!-- /.box-header -->
    <!--<expenses-edit-form style="padding: 10px;"></expenses-edit-form>-->
    <div class="box-body">
      <div class="table">
        <!--<div class="table-responsive">-->
        <table class="table no-margin table-hover">
          <thead>
          <tr>
            <th class="col-xs-1 col-md-1">{{ t('Date') }}</th>
            <th class="col-xs-8 col-md-10">{{ t('Shopping') }}</th>
            <th class="col-xs-3 col-md-1">{{ t('Amount') }}</th>
          </tr>
          </thead>
          <tbody v-if="showExpensesList">
            <expenses-by-day
              v-for="(expenses, key) in groupedExpenses"
              :key="key"
              :date="key"
              :items="expenses"
            />
          </tbody>
          <tbody v-else>
            <tr class="text-center">
              <td colspan="3" style="color: rgb(148, 111, 111)">Ще нема витрат на харчування...</td>
            </tr>
          </tbody>
        </table>
      </div>
      <!-- /.table-responsive -->
    </div>
    <!-- /.box-body -->
    <div class="box-footer clearfix">
      <button class="btn btn-sm btn-info btn-flat pull-left" @click="addExpense">{{ t('Add New') }}</button>
      <a href="javascript:void(0)" class="btn btn-sm btn-default btn-flat pull-right" @click="toggleDisplayListCount" v-text="toggleDisplayCountBtnText" v-if="showExpensesList"></a>
      <div class="clearfix"></div>
    </div>
    <!-- /.box-footer -->
  </div>
</template>

<script>
import ExpensesByDay from '@/views/expenses/food/ExpensesByDay'
// import $ from 'jquery'

export default {
  name: 'FoodExpensesTable',
  components: {
    ExpensesByDay
  },
  data () {
    return {
      showAll: false
    }
  },
  computed: {
    groupedExpenses () {
      let groupedExpenses = this.$store.getters.getFoodExpensesGroupedByDate
      let groupedExpensesSliced = {}
      let counter = 0
      for (let key in groupedExpenses) {
        if (this.showAll || counter < 3) {
          groupedExpensesSliced[key] = groupedExpenses[key]
        }
        counter++
      }
      return groupedExpensesSliced
    },
    toggleDisplayCountBtnText () {
      return this.showAll ? this.t('Hide All') : this.t('View All')
    },
    showExpensesList () {
      return (Object.keys(this.groupedExpenses)).length > 0
    }
  },
  methods: {
    addExpense () {
      this.$store.dispatch('addNewEditExpense', true)
      // $('html, body').animate({scrollTop: 0}, 500)
    },
    toggleDisplayListCount () {
      this.showAll = !this.showAll
    }
  }
}
</script>