"use strict";

import Vue from 'vue'
import axios from 'axios'
import Auth from '@/services/auth'
import router from '@/router'
import store from '@/store'
import snakeToCamelCase from 'camelcase-keys-deep'
import camelToSnakeCase from 'decamelize-keys-deep'

Vue.prototype.$axios = axios

axios.defaults.baseURL = process.env.VUE_APP_API_URL
axios.defaults.withCredentials = true

axios.interceptors.request.use(request => {
  request.headers['Authorization'] = Auth.getAuthHeader()
  request.headers['X-Requested-With'] = 'XMLHttpRequest'
  request.data = camelToSnakeCase(request.data)
  return request
}, error => Promise.reject(error))

axios.interceptors.response.use(response => {
  return snakeToCamelCase(response.data)
}, error => {
  return new Promise((resolve, reject) => {
    const originalRequest = error.config
    if (error.response.status) {
      switch (error.response.status) {
        case 401:
          if (!originalRequest._retry) {
            originalRequest._retry = true
            Auth.refreshToken()
              .then(response => {
                if (response) {
                  axios.defaults.headers.common['Authorization'] = Auth.getAuthHeader()
                  originalRequest.headers['Authorization'] = Auth.getAuthHeader()

                  resolve(axios(originalRequest))
                }
              })
              .catch(() => {
                Auth.clearJwtAuth()
                store.dispatch('logoutUser')
                router.push({name: 'Login'})
              })
          } else {
            Auth.clearJwtAuth()
            store.dispatch('logoutUser')
            router.push({name: 'Login'})
          }
          break
        default:
          return reject(error.response.data)
      }
    } else {
      return reject(error)
    }
  })
})
