import Vue from 'vue'
import * as types from '../mutation-types'
import DebtApi from '@/services/debts'

const state = {
  debts: []
}

const getters = {
  getDebts: state => state.debts,
  getDebtById: state => id => state.debts.find(expenseCategory => expenseCategory.id === id),
  getFirstDebt: state => state.debts[0],
  getAmountYourDebts: (state, getters) => state.debts.filter(item => item.isYour).reduce((amount, item) => {
    let currencyCode = getters.getCurrencyById(item.currency).code
    let itemAmount = item.amount
    if (currencyCode === 'USD') {
      // Add convert
      itemAmount *= 26
    }
    return amount + parseInt(itemAmount)
  }, 0),
  getAmountDebts: (state, getters) => state.debts.filter(item => !item.isYour).reduce((amount, item) => {
    let currencyCode = getters.getCurrencyById(item.currency).code
    let itemAmount = item.amount
    if (currencyCode === 'USD') {
      // Add convert
      itemAmount *= 26
    }
    return amount + parseInt(itemAmount)
  }, 0)
}

const actions = {
  getDebts ({ state, commit }) {
    return DebtApi.getAll()
      .then(debts => {
        commit(types.CLEAR_DEBTS)
        debts.forEach(item => {
          commit(types.SET_DEBT, item)
        })
      })
      .catch((error) => {
        console.error(error)
      })
  },
  deleteDebt ({ commit }, id) {
    return DebtApi.delete(id)
      .then(() => {
        commit(types.DELETE_DEBT, id)
        return true
      })
      .catch(error => {
        console.log(error)
        return false
      })
  }
}

const mutations = {
  [types.SET_DEBT] (state, debt) {
    debt.amount = parseInt(debt.amount)
    debt.isYour = !!debt.isYour
    state.debts.push(debt)
  },
  [types.UPDATE_DEBT] (state, updatedDebt) {
    state.debts.forEach((expenseCategory, index) => {
      if (expenseCategory.id === updatedDebt.id) {
        Vue.set(state.debts, index, updatedDebt)
      }
    })
  },
  [types.DELETE_DEBT] (state, id) {
    state.debts.forEach((item, index) => {
      if (item.id === id) {
        state.debts.splice(index, 1)
      }
    })
  },
  [types.CLEAR_DEBTS] (state) {
    state.debts = []
  }
}

export default {
  state,
  mutations,
  actions,
  getters
}
