<template>
  <div>
    <div class="row">
      <div class="col-xs-7">
        <input type="text" class="form-control"
               v-model="name"
               placeholder="Назва категорiї..."
        >
      </div>
      <div class="col-xs-2" v-click-outside="hideColorpicker" style="padding-left: 0; padding-right: 0;">
        <span class="label"
              :style="{ backgroundColor: color, fontSize: sizeColorBtn + 'px' }"
              @click="toggleColorpicker"
        >
          Color
        </span>
        <colorpicker class="colorpicker"
                     v-show="colorpicker.visible"
                     :value="color"
                     @input="updateColor"
        />
      </div>
      <div class="col-xs-3">
        <button class="btn btn-sm btn-info" :style="{ padding: paddingHandleBtns + 'px' }" @click="save">
          <i class="fa fa-check" :style="{ fontSize: sizeHandleBtns + 'px' }"></i>
        </button>
        <button class="btn btn-sm" :style="{ padding: paddingHandleBtns + 'px' }" @click="cancel">
          <i class="fa fa-times" :style="{ fontSize: sizeHandleBtns + 'px' }"></i>
        </button>
      </div>
    </div>

    <div class="text-center margin-top-20" v-show="status.tryDelete">
      <h4 style="color: orangered;">Видалити категорiю?</h4>
      <button class="btn btn-sm btn-info" @click="deleteCategory">Видалити</button>
      <button class="btn btn-sm" @click="cancel">{{ t('Cancel') }}</button>
    </div>

    <div v-if="status.isSaved">
      Saved
    </div>
    <div v-if="status.isFailure">
      Failure
    </div>
    <div v-if="status.isLoading">
      Loading
    </div>
  </div>
</template>

<script>
import { Sketch } from 'vue-color'
import { mapState } from 'vuex'
import * as types from '@/store/mutation-types'

export default {
  name: 'ExpenseCategoryEditForm',
  components: {
    colorpicker: Sketch
  },
  props: {
    id: {
      type: Number
    },
    sizeColorBtn: {
      type: String,
      default: '18'
    },
    sizeHandleBtns: {
      type: String,
      default: '12'
    },
    paddingHandleBtns: {
      type: String
    }
  },
  data () {
    return {
      colorpicker: {
        visible: false
      }
    }
  },
  computed: {
    ...mapState({
      status: state => state.editExpenseCategory.status,
      color: state => state.editExpenseCategory.color
    }),
    name: {
      get () {
        return this.$store.state.editExpenseCategory.name
      },
      set (value) {
        this.$store.commit(types.UPDATE_EXPENSE_CATEGORY_NAME, value)
      }
    },
    isEdible: {
      get () {
        return this.$store.state.editExpenseCategory.isEdible
      },
      set (value) {
        this.$store.commit(types.UPDATE_EXPENSE_CATEGORY_IS_EDIBLE, value)
      }
    }
  },
  watch: {
    'status.isSaved': function (value) {
      if (value) {
        setTimeout(() => {
          this.cancel()
        }, 1000)
      }
    }
  },
  methods: {
    save () {
      if (this.id) {
        this.$store.dispatch('updateExpenseCategory', this.id)
      } else {
        this.$store.dispatch('addExpenseCategory')
      }
    },
    updateColor (color) {
      this.$store.commit(types.UPDATE_EXPENSE_CATEGORY_COLOR, color.hex)
    },
    toggleColorpicker () {
      this.colorpicker.visible = !this.colorpicker.visible
    },
    hideColorpicker () {
      this.colorpicker.visible = false
    },
    cancel () {
      this.$emit('cancel')
    },
    deleteCategory () {
      this.$store.dispatch('deleteExpenseCategory', this.id)
    }
  }
}
</script>

<style scoped>
.label {
  line-height: 32px;
  font-size: 18px;
}

.colorpicker {
  position: absolute;
  left: -90%;
  top: -315px;
  z-index: 100;
}

.margin-top-20 {
  margin-top: 20px;
}
</style>