<template>
  <tr>
    <td>
      <span v-show="!showEditForm">{{ name }}</span>
      <input type="text" placeholder="Name..." class="form-control" :value="name" v-show="showEditForm" @input="update($event.target.value, 'name')">

      <div class="text-right margin-top-20" v-show="showEditForm">
        <button class="btn btn-sm btn-info" @click="saveType">Save</button>
        <button class="btn btn-sm" @click="cancelEditType">Cancel</button>
      </div>
    </td>
    <td class="text-center">
      <span v-show="!showEditForm">{{ unit }}</span>
      <input type="text" placeholder="Unit..." class="form-control" :value="unit" v-show="showEditForm" @input="update($event.target.value, 'unit')">
    </td>
    <td>
      <p v-show="!showEditForm" v-for="rateRule in rateRules" style="margin-bottom: 1px;">{{ rateRule.rate }} <span v-if="rateRule.limit > 0" style="font-size: 11px;">до</span> {{ rateRule.limit }}</p>

      <div v-show="showEditForm" class="text-center">
        <!--<input type="text" placeholder="Rate..." class="form-control" :value="rate" @input="update($event.target.value, 'rate')">-->
        <div style="margin-bottom: 5px; position: relative;" v-for="rateRule in rateRules">
          <div class="input-group">
            <div class="input-group-btn">
              <input type="text" placeholder="Rate..." class="form-control rate" :value="rateRule.rate">
            </div>
            <div class="input-group-btn">
              <input type="text" class="form-control rule" :value="rateRule.limit">
              <label v-if="!rateRule.limit">&#8734;</label>
            </div>
          </div>
          <div class="divider">/</div>

        </div>

        <div style="margin-top: 5px; cursor: pointer">
          <i class="fa fa-plus" style="color: green;"></i>
          <span>rule...</span>
        </div>
      </div>
    </td>
    <td>
      <div class="currency">
        <div class="dropdown-toggle" :class="{ 'form-control': showEditForm}" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
          {{ selectedCurrency.code }}
        </div>
        <ul class="dropdown-menu" v-show="showEditForm">
          <li @click="update(currency, 'currency')" v-for="currency in currencies"><a href="javascript:void(0);">{{ currency.code }}</a></li>
        </ul>
      </div>
    </td>
    <td>
      <i class="edit-btn fa fa-pencil" v-show="!showEditForm" @click="showEditForm = true"></i>
      <i class="edit-btn delete fa fa-trash" v-show="!showEditForm" @click="deleteType"></i>
    </td>
  </tr>
</template>

<script>
import { mapGetters } from 'vuex'
import * as types from '@/store/mutation-types'

export default {
  name: 'UtilityType',
  props: {
    uuid: {
      type: String,
      required: true
    },
    name: {
      type: String,
      required: true
    },
    rateRuleIds: {
      type: Array,
      required: true
    },
    currencyId: {
      type: [Number, String],
      required: true
    },
    unit: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      showEditForm: false,
      input4: '',
      input5: ''
    }
  },
  computed: {
    ...mapGetters({
      currencies: 'getCurrencies'
    }),
    currency () {
      return this.$store.getters.getCurrencyById(this.currencyId)
    },
    selectedCurrency () {
      let currency = {
        code: ''
      }
      this.currencies.forEach((item, index) => {
        if (item.id === this.currency.id) {
          currency = this.currencies[index]
        }
      })
      return currency
    },
    rateRules () {
      return this.$store.getters.getUtilityRateRuleByIds(this.rateRuleIds)
    }
  },
  methods: {
    saveType () {
      this.$store.dispatch('saveUtilityType', this.uuid)
        .then(() => {
          this.showEditForm = false
        })
    },
    update (value, type) {
      this.$store.commit(types.UPDATE_UTILITY_TYPE, {
        uuid: this.uuid,
        field: type,
        value: value
      })
    },
    cancelEditType () {
      this.$store.dispatch('cancelEditUtilityType', this.uuid)
        .then(() => {
          this.showEditForm = false
        })
    },
    deleteType () {
      this.$store.commit(types.ADD_UTILITY_TYPE_STATUS, {
        uuid: this.uuid,
        status: 'deleted'
      })
    }
  },
  created () {
    this.$bus.$on('show-edit-utility-type-form', (uuid) => {
      if (uuid === this.uuid) {
        this.showEditForm = true
      }
    })
  }
}
</script>

