<template>
  <v-app>
    <template v-if="isLoggedIn">
      <Sidebar :mini="miniVariant"/>
      <AppHeader @menu-toggle="miniVariant = !miniVariant"/>
      <v-content id="main">
        <div class="mb-5 px-3 mt-3">
          <transition name="fade" mode="out-in">
            <router-view/>
          </transition>
        </div>
        <AppFooter/>
      </v-content>
    </template>
    <router-view v-else/>
  </v-app>
</template>

<script>
import { mapState } from 'vuex'
import Sidebar from '@/components/TheSidebar'
import Header from '@/components/TheHeader'
import Footer from '@/components/TheFooter'
import Auth from '@/views/Auth'
import Loader from '@/components/Loader'

export default {
  name: 'App',
  components: {
    Sidebar,
    'AppHeader': Header,
    'AppFooter': Footer,
    Auth,
    Loader
  },
  data () {
    return {
      miniVariant: false
    }
  },
  computed: {
    ...mapState({
      isLoggedIn: state => state.authUser.isLoggedIn
    })
  },
  created () {
    if (this.isLoggedIn) {
      this.$store.dispatch('fetchUser')
    }
  }
}
</script>

<style lang="stylus">
@import "style/base.styl"

#main {
  background-color #f2f3fa
  transition .4s cubic-bezier(.4,0,.2,1)
}
</style>
