<template>
  <div class="box box-success">
    <div class="box-header with-border">
      <h3 class="box-title">{{ t(type) }}</h3>
      <div class="box-tools pull-right">
        <span style="font-size: 18px;">{{ amount }} UAH</span>

        <button type="button" class="btn btn-box-tool" data-widget="collapse"><i class="fa fa-minus"></i></button>
        <button type="button" class="btn btn-box-tool" data-widget="remove"><i class="fa fa-times"></i></button>
      </div>
    </div>
    <!-- /.box-header -->
    <div class="box-body">
      <div class="table">
        <!--<div class="table-responsive">-->
        <table class="table no-margin table-hover">
          <thead>
          <tr>
            <th class="col-xs-3 col-md-3">{{ t('Category') }}</th>
            <th class="col-xs-6 col-md-7">{{ t('Name') }}</th>
            <th class="col-xs-2 col-md-2 text-center">{{ t('Price') }}</th>
            <th class="col-xs-1 hidden-xs"></th>
          </tr>
          </thead>
          <tbody v-if="showExpensesList">
            <general-expense
              v-for="(expense, key) in expenses"
              :key="key"
              :description="expense.description"
              :category="getExpenseCategoryById(expense.category)"
              :price="expense.price"
              :date="expense.date"
              :currency="getCurrencyById(expense.currency).code"
            />
          </tbody>
          <tbody v-else>
            <tr class="text-center">
              <td colspan="3" style="color: rgb(148, 111, 111)">Ще нема витрат на {{ t(type) }}...</td>
            </tr>
          </tbody>
        </table>
      </div>
      <!-- /.table-responsive -->
    </div>
    <!-- /.box-body -->
    <div class="box-footer clearfix">
      <button class="btn btn-sm btn-info btn-flat pull-left" @click="addExpense">{{ t('Add New') }}</button>
      <a href="javascript:void(0)" class="btn btn-sm btn-default btn-flat pull-right" @click="toggleDisplayListCount" v-text="toggleDisplayCountBtnText" v-if="showExpensesList"></a>
      <div class="clearfix"></div>
    </div>
    <!-- /.box-footer -->
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import GeneralExpense from '@/views/expenses/partials/GeneralExpense'
// import $ from 'jquery'

export default {
  name: 'ExpensesTable',
  components: {
    GeneralExpense
  },
  props: {
    isGeneral: {
      type: Boolean,
      required: true
    }
  },
  data () {
    return {
      showAll: false,
      type: this.isGeneral ? 'general' : 'personal'
    }
  },
  computed: {
    amount () {
      return this.$store.getters.getAmountExpensesByType(this.type)
    },
    ...mapGetters([
      'getExpenseCategoryById',
      'getCurrencyById'
    ]),
    expenses () {
      let expenses = this.$store.getters.getPersonalExpenses.reverse()
      if (this.isGeneral) {
        expenses = this.$store.getters.getGeneralExpenses.reverse()
      }
      if (!this.showAll) {
        return expenses.slice(0, 5)
      }
      return expenses
    },
    toggleDisplayCountBtnText () {
      return this.showAll ? this.t('Hide All') : this.t('View All')
    },
    showExpensesList () {
      return this.expenses.length > 0
    }
  },
  methods: {
    addExpense () {
      this.$store.dispatch('addNewEditExpense', this.isGeneral)
      // $('html, body').animate({scrollTop: 0}, 500)
    },
    toggleDisplayListCount () {
      this.showAll = !this.showAll
    }
  }
}
</script>