import * as types from '../mutation-types'
import UtilityRateRulesAPI from '@/services/utilityRateRule'

const state = {
  list: []
}

const getters = {
  getUtilityRateRules: state => {
    return state.utilityRateRules
  },
  getUtilityRateRuleById: state => id => {
    return state.utilityRateRules.find(utilityRateRule => utilityRateRule.id === id)
  },
  getUtilityRateRuleByIds: (state, getters) => ids => {
    let rateRules = []
    ids.forEach(id => {
      rateRules.push(getters.getUtilityRateRuleById(id))
    })
    return rateRules
  }
}

const actions = {
  getUtilityRateRules ({ commit }) {
    return UtilityRateRulesAPI.getAll()
      .then(utilityRateRules => {
        commit(types.SET_UTILITY_RATE_RULES, utilityRateRules)
      })
      .catch((error) => {
        console.error(error)
      })
  }
}

const mutations = {
  [types.SET_UTILITY_RATE_RULES] (state, utilityRateRules) {
    state.list = []
    utilityRateRules.forEach(({ id, rate, limit }) => {
      state.list.push({
        id,
        rate,
        limit
      })
    })
  }
}

export default {
  state,
  mutations,
  actions,
  getters
}
