<template>
  <div>
    <div class="box box-warning">
      <div class="box-header with-border">
        <h3 class="box-title">Показання та сумма</h3>
        <div class="box-tools pull-right">
          <button type="button" class="btn btn-box-tool" data-widget="collapse"><i class="fa fa-minus"></i></button>
          <button type="button" class="btn btn-box-tool" data-widget="remove"><i class="fa fa-times"></i></button>
        </div>
      </div>
      <div class="box-body">
        <ActiveMonthUtility/>
      </div>
      <div class="box-year">
        <div class="row">
          <MonthUtility class="col-sm-2"
                        v-for="(groupedUtility, keyDate) in groupedUtilities"
                        :key="keyDate"
                        :init-date="keyDate"
          />
        </div>
      </div>
      <div class="box-footer clearfix">
        <AddNewMonth class="pull-left"/>
        <a href="javascript:void(0)" class="btn btn-sm btn-default btn-flat pull-right" @click="toggleDisplayMonths()" v-text="showMonthsBtnText"></a>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import ActiveMonthUtility from '@/views/utilities/ActiveMonthUtility'
import MonthUtility from '@/views/utilities/MonthUtility'
import AddNewMonth from '@/views/utilities/AddNewMonth'

export default {
  name: 'UtilitiesTable',
  components: {
    ActiveMonthUtility,
    MonthUtility,
    AddNewMonth
  },
  data () {
    return {
      isOpen: false
    }
  },
  computed: {
    ...mapGetters({
      groupedUtilities: 'getGroupedUtilities'
    }),
    ...mapState({
      utilities: state => state.utilities.utilities
    }),
    showMonthsBtnText () {
      return !this.isOpen ? this.t('View All') : this.t('Hide All')
    }
  },
  methods: {
    toggleDisplayMonths () {
      this.isOpen = !this.isOpen
    }
  }
}
</script>

<style scoped>
@media (min-width: 768px) {
  .box {
    margin-bottom: 0;
  }
}

.box-year {
  padding: 5px;
}

.box-year .row {
  margin: 0;
}

.box-year .col-sm-2 {
  padding: 2px;
}
</style>