<template>
  <tr>
    <td v-show="showEditForm" colspan="3">
      <transition name="page" mode="out-in">
        <expense-category-edit-form
          v-show="showEditForm"
          :id="id"
          @cancel="cancelEdit"
          sizeHandleBtns="10"
          paddingHandleBtns="5"
        />
      </transition>
    </td>
    <td v-show="!showEditForm">
      {{ name }}
    </td>
    <td v-show="!showEditForm">
      <span class="label" :style="{ 'background-color': color }" v-show="!showEditForm">Color</span>
    </td>
    <td v-show="!showEditForm">
      <i class="edit-btn fa fa-pencil" @click="edit"></i>
      <i class="edit-btn delete fa fa-trash" @click="tryDelete"></i>
    </td>
  </tr>
</template>

<script>
import ExpenseCategoryEditForm from '@/views/expenses/categories/ExpenseCategoryEditForm'
import * as types from '@/store/mutation-types'
import { mapState } from 'vuex'

export default {
  name: 'ExpenseCategory',
  components: {
    ExpenseCategoryEditForm
  },
  props: {
    id: {
      type: Number,
      required: true
    },
    name: {
      type: String,
      required: true
    },
    color: {
      type: String,
      required: true
    }
  },
  computed: {
    ...mapState({
      showEditForm (state) {
        return state.editExpenseCategory.id === this.id
      }
    })
  },
  methods: {
    tryDelete () {
      this.edit()
      this.$store.commit(types.SET_EXPENSE_CATEGORY_TRY_DELETE_STATUS)
    },
    edit () {
      this.$store.dispatch('setEditExpenseCategory', this.id)
    },
    cancelEdit () {
      this.$store.commit(types.CLEAR_EXPENSE_CATEGORY)
    }
  }
}
</script>

<style scoped>
.label {
  margin: 0 2px;
  font-size: 11px;
}

tr td:last-child {
  position: relative;
}

.edit-btn {
  transition: opacity 0.1s linear;
  font-size: 16px;
  position: absolute;
  top: 3px;
  cursor: pointer;
  color: rgb(0, 166, 90);
  right: 25px;
  opacity: 0;
}

.edit-btn.delete {
  color: #dd4b39;
  right: 5px;
}

tr:hover .edit-btn {
  opacity: 1;
}
</style>