import * as types from '../mutation-types'
import DebtApi from '@/services/debts'

const state = {
  id: '',
  isYour: false,
  name: '',
  amount: '',
  currency: '',
  description: '',
  status: {
    isFailure: false,
    isLoading: false,
    isSaved: false
  }
}

const actions = {
  setEditDebt ({ commit, getters }, id) {
    let debt = {}
    if (id) {
      debt = getters.getDebtById(id)
    } else {
      debt['currency'] = getters.getFirstCurrency.id
    }
    commit(types.INIT_EDIT_DEBT, debt)
  },
  addDebt ({ state, commit }) {
    commit(types.SET_DEBT_LOADING_STATUS)
    let debt = {
      isYour: !!state.isYour,
      name: state.name,
      amount: state.amount,
      currency: state.currency,
      description: state.description
    }
    DebtApi.store(debt)
      .then(id => {
        debt['id'] = id
        commit(types.SET_DEBT, debt)
        commit(types.SET_DEBT_SAVED_STATUS)
      })
      .catch(error => {
        console.log(error)
        commit(types.SET_DEBT_FAILURE_STATUS)
      })
  },
  updateDebt ({ commit }, id) {
    commit(types.SET_DEBT_LOADING_STATUS)
    let debt = {
      id: id,
      isYour: !!state.isYour,
      name: state.name,
      amount: state.amount,
      currency: state.currency,
      description: state.description
    }
    DebtApi.update(id, debt)
      .then(() => {
        commit(types.UPDATE_DEBT, debt)
        commit(types.SET_DEBT_SAVED_STATUS)
      })
      .catch(error => {
        console.log(error)
        commit(types.SET_DEBT_FAILURE_STATUS)
      })
  }
}

const mutations = {
  [types.INIT_EDIT_DEBT] (state, debt) {
    state.isYour = !!debt.isYour || ''
    state.name = debt.name || ''
    state.amount = debt.amount || ''
    state.currency = debt.currency || ''
    state.description = debt.description || ''
    state.status.isSaved = false
    state.status.isFailure = false
    state.status.isLoading = false
  },
  [types.UPDATE_DEBT_NAME] (state, value) {
    state.name = value
  },
  [types.UPDATE_DEBT_IS_YOUR] (state, value) {
    state.isYour = value
  },
  [types.UPDATE_DEBT_AMOUNT] (state, value) {
    state.amount = parseInt(value)
  },
  [types.UPDATE_DEBT_CURRENCY] (state, value) {
    state.currency = value
  },
  [types.UPDATE_DEBT_DESCRIPTION] (state, value) {
    state.description = value
  },
  [types.SET_DEBT_LOADING_STATUS] (state) {
    state.status.isLoading = true
    state.status.isFailure = false
    state.status.isSaved = false
  },
  [types.SET_DEBT_FAILURE_STATUS] (state) {
    state.status.isLoading = false
    state.status.isFailure = true
    state.status.isSaved = false
  },
  [types.SET_DEBT_SAVED_STATUS] (state) {
    state.status.isLoading = false
    state.status.isFailure = false
    state.status.isSaved = true
  }
}

export default {
  state,
  mutations,
  actions
}
