<template>
  <div>
    <div class="text-center" @click="openEditModal()" style="cursor: pointer;">
      <p style="color: rgb(148, 111, 111); font-size: 16px;">Що нового сьогодні?</p>
    </div>
    <transition name="bounce" enter-active-class="bounceInLeft" leave-active-class="bounceOutRight">
      <div class="modal modal-expenses-edit" v-show="isVisible.modal">
        <div class="modal-dialog">
          <div class="modal-content row">
            <v-touch v-on:swiperight="showExpensesList()" class="modal-sidebar col-xs-2">
              <button type="button" class="close" @click="closeEditModal()"><span aria-hidden="true">×</span></button>
              <hr style="color: #e5e5e5; margin-top: 5px; margin-bottom: 10px;">
              <transition-group name="fade" tag="div" class="list">
                <div v-for="expense in expenses" v-show="isVisible.editForm" :style="{ 'background-color': expense.category.color }" :key="expense.uuid" class="type-icon" @click="editExpense(expense)">
                  <v-touch v-on:swiperight="showExpensesList()">
                    <span><i class="fa" :class="typeIcons[expense.type.slug]"></i></span>
                  </v-touch>
                </div>
              </transition-group>
            </v-touch>
            <v-touch v-on:swipeleft="hideExpensesList()" class="modal-body col-xs-10">
              <transition-group name="fade" tag="div">
                <div key="expensesList" v-show="!isVisible.editForm" class="expenses-list">
                  <v-touch v-on:swipeleft="hideExpensesList()" class="expense" v-for="expense in expenses" :key="expense.uuid" v-show="!isVisible.editForm" @click="editExpense(expense)">
                    <div class="type">
                      <div :style="{ 'background-color': expense.category.color }" class="type-icon">
                        <span><i class="fa" :class="typeIcons[expense.type.slug]"></i></span>
                      </div>
                    </div>
                    <div class="category">{{ expense.category.name }}</div>
                    <div class="price">
                      {{ expense.price }}
                      <span v-html="currencySign[expense.currency.code]"></span>
                    </div>
                    <div class="clearfix"></div>
                  </v-touch>
                </div>
                <div key="editForm" class="edit" v-show="!isLoaded && isVisible.editForm">
                    <div v-click-outside="closeDescription">
                      <div class="input-group category">
                        <input type="text" v-model="queryCategories" @change="updateCurrentExpense" class="form-control" style="border-right: none;" placeholder="Введите категорию...">
                        <span class="input-group-addon" @click="clearQueryCategories">×</span>
                      </div>
                      <div class="input-group" style="margin-top: 7px;">
                        <input type="number" class="form-control text-right price" placeholder="0.00" v-model="expense.price" @change="updateCurrentExpense" number>
                        <div class="input-group-addon type" style="border-left: none;">
                          <div class="dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                            <i class="fa" :class="typeIcons[expense.type.slug]"></i>
                          </div>
                          <ul class="dropdown-menu">
                            <li @click="setExpenseType(type)" v-for="type in types">
                              <a href="javascript:void(0);">
                                <i class="fa" :class="typeIcons[type.slug]"></i> {{ type.name }}
                              </a>
                            </li>
                          </ul>
                        </div>
                        <div class="input-group-addon" @click="openDescription">
                          <div class="dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                            <i class="fa fa-edit"></i>
                          </div>
                        </div>
                      </div>
                      <transition name="page" mode="out-in">
                        <textarea style="margin-top: 10px;" placeholder="Введiть товари через кому..." class="form-control" v-show="isVisible.description" v-model="expense.description"></textarea>
                      </transition>
                    </div>
                    <div class="list">
                      <div v-for="category in categories" :key="category.id" class="item" @click="setExpenseCategory(category)">
                        <i class="fa fa-square" :style="{ 'color': category.color }"></i>{{ category.name }}
                      </div>
                    </div>
                  </div>
                <div key="loadedForm" v-show="isLoaded">
                  <div class="text-center center" style="color: #65bd65; margin-top: 50%;">
                    <i class="fa fa-check" style="font-size: 48px"></i>
                    <p style="font-size: 24px;">Loaded succeed:)</p>
                  </div>
                </div>
              </transition-group>
            </v-touch>
            <div class="modal-footer">
              <md-button class="md-icon-button md-raised md-accent pull-left" data-dismiss="modal" @click="saveExpenses">
                <md-icon>done</md-icon>
              </md-button>
              <md-button class="md-icon-button md-raised md-primary" @click="addExpense">
                <md-icon>add</md-icon>
              </md-button>
            </div>
          </div>
          <!-- /.modal-content -->
        </div>
        <!-- /.modal-dialog -->
      </div>
    </transition>
  </div>
</template>

<script>
import { generateUUID, getDateWithoutTime } from '@/utils/helpers'
// import Datepicker from 'vuejs-datepicker'
import { mapGetters } from 'vuex'

export default {
  name: 'ExpensesEditFormMobile',
  data () {
    return {
      expenses: [],
      expense: {},
      date: getDateWithoutTime().format(),
      queryCategories: '',
      isVisible: {
        modal: false,
        description: false,
        editForm: true
      },
      typeIcons: {
        general: 'fa-users',
        personal: 'fa-user',
        food: 'fa-cutlery'
      },
      currencySign: {
        'UAH': '&#8372;',
        'USD': '&#036;',
        'EUR': '&#128;'
      }
    }
  },
  computed: {
    ...mapGetters({
      expenseCategories: 'getExpenseCategories',
      types: 'getExpenseTypes'
    }),
    categories () {
      let categories = this.expenseCategories
      return this.queryCategories ? categories.filter((category) => {
        return (category.name.toLowerCase().trim().indexOf(this.queryCategories.toLowerCase().trim()) === 0)
      }) : categories
    },
    dateString () {
      return getDateWithoutTime(this.date).format()
    },
    isLoaded () {
      let countExpenses = this.expenses.length
      let counter = 0
      this.expenses.forEach((expense) => {
        if (expense.status === 'loaded') {
          counter++
        }
      })
      return counter === countExpenses && countExpenses !== 0
    }
  },
  watch: {
    isLoaded (value) {
      if (value) {
        setTimeout(() => {
          this.closeEditModal()
        }, 3000)
      }
    }
  },
  methods: {
    openEditModal () {
      this.isVisible.modal = true
      this.hideExpensesList()
      this.$bus.$emit('open-modal')
    },
    closeEditModal () {
      this.expenses = []
      this.isVisible.modal = false
      this.$bus.$emit('close-modal')
    },
    openDescription () {
      this.isVisible.description = true
    },
    closeDescription () {
      this.isVisible.description = false
    },
    showExpensesList () {
      this.isVisible.editForm = false
    },
    hideExpensesList () {
      this.isVisible.editForm = true
    },
    addExpense () {
      if (this.expense.price !== '' && this.expense.category !== '') {
        let sameFindExpenseIndex = this.expenses.findIndex(expense => expense.category.id === this.expense.category.id && expense.type.id === this.expense.type.id)
        if (sameFindExpenseIndex !== -1) {
          this.expenses[sameFindExpenseIndex].price += this.expense.price
        } else {
          this.expenses.push(this.expense)
        }
        this.clearExpense()
      }
    },
    editExpense (expense) {
      this.expense = expense
      this.queryCategories = expense.category.name
      this.hideExpensesList()
    },
    clearExpense () {
      this.initExpense()
      this.clearQueryCategories()
    },
    clearQueryCategories () {
      this.queryCategories = ''
    },
    initExpense () {
      this.expense = {
        status: 'new',
        previousStatus: '',
        uuid: generateUUID(),
        id: '',
        type: this.$store.getters.getExpenseTypeBySlug('food'),
        currency: this.$store.getters.getCurrencyByCode('UAH'),
        price: '',
        description: '',
        category: ''
      }
    },
    setExpenseCategory (item) {
      this.updateCurrentExpense()
      this.queryCategories = item.name
      this.expense.category = item
      this.setStatusUpdateForExpense(this.expense.uuid)
    },
    setExpenseType (type) {
      this.updateCurrentExpense()
      this.expense.type = type
      this.setStatusUpdateForExpense(this.expense.uuid)
    },
    updateCurrentExpense () {
      this.setStatusUpdateForExpense(this.expense.uuid)
    },
    setStatusUpdateForExpense (uuid) {
      this.setStatusForExpense(uuid, 'update')
    },
    setStatusLoadedForExpense (uuid) {
      this.setStatusForExpense(uuid, 'loaded')
    },
    setStatusDeleteForExpense (uuid) {
      this.setStatusForExpense(uuid, 'delete')
    },
    setStatusForExpense (uuid, status) {
      this.expenses.forEach((expense, index) => {
        if (expense.uuid === uuid) {
          expense.previousStatus = expense.status
          expense.status = status
          this.expenses[index] = expense
        }
      })
    },
    saveExpenses () {
      this.expenses.forEach((expense) => {
        if (expense.price !== '' && expense.category) {
          switch (true) {
            // create expense
            case expense.status === 'new':
              this.$store.dispatch('addExpense', {
                date: this.date,
                category: expense.category,
                type: expense.type,
                currency: expense.currency,
                price: expense.price,
                description: expense.description
              })
                .then(() => {
                  this.setStatusLoadedForExpense(expense.uuid)
                })
              break
            // update created expense
            case expense.status === 'updated':
              this.$store.dispatch('updateExpense', {
                id: expense.id,
                date: this.date,
                category: expense.category,
                type: expense.type,
                currency: expense.currency,
                price: expense.price,
                description: expense.description
              })
                .then(() => {
                  this.setStatusLoadedForExpense(expense.uuid)
                })
              break
            // delete created expense
            case expense.status === 'deleted':
              this.$store.dispatch('deleteExpense', this.expense.id)
                .then(() => {
                  this.setStatusLoadedForExpense(expense.uuid)
                })
              break
            default:
              this.setStatusLoadedForExpense(expense.uuid)
              break
          }
        }
      })
    }
  },
  created () {
    this.initExpense()
    this.$bus.$on('add-expense', () => {
      this.openEditModal()
    })
  }
}
</script>

