<template>
  <div @mouseenter="sidebarMouseEnter" @mouseleave="sidebarMouseLeave">
    <v-navigation-drawer
      persistent
      :mini-variant="miniVariant"
      width="230"
      enable-resize-watcher
      fixed
      app
      id="sidebar"
      dark
    >
      <v-toolbar flat class="bg-sidebar-header">
        <v-list>
          <v-list-tile color="white">
            <v-list-tile-title class="headline text-lg-center font-weight-bold" v-text="miniVariant ? 'HA' : 'Home Assistant'" style="height: 34px;">
              HA
            </v-list-tile-title>
          </v-list-tile>
        </v-list>
      </v-toolbar>

      <v-list class="sidebar-links">
        <template v-for="item in items">
          <v-list-group
            v-if="item.group"
            :key="item.title"
            v-model="item.visible"
            @click="openLink(item)"
            :class="{'blue-grey--text text--lighten-2': !item.visible, 'white--text bg-sidebar-collapse-link': item.visible }"
          >
            <v-list-tile slot="activator">
              <v-list-tile-action>
                <v-icon :color="item.visible ? 'white' : 'blue-grey lighten-2'">{{ item.icon }}</v-icon>
              </v-list-tile-action>
              <v-list-tile-title>{{ item.title }}</v-list-tile-title>
            </v-list-tile>
            <template v-if="!miniVariant">
              <v-list-tile
                v-for="groupItem in item.list"
                :to="groupItem.route"
                :key="groupItem.route"
                @click="checkVisibleLinkGroup()"
                class="blue-grey--text text--lighten-2"
                active-class="white--text bg-sidebar-active-link"
              >
                <v-list-tile-title v-text="groupItem.title" class="font-weight-regular"></v-list-tile-title>
              </v-list-tile>
            </template>
          </v-list-group>

          <v-list-tile
            v-else
            :key="item.route"
            :to="item.route"
            @click="checkVisibleLinkGroup()"
            class="blue-grey--text text--lighten-2"
            active-class="white--text bg-sidebar-active-link"
          >
            <v-list-tile-action>
              <v-icon :color="$route.path === item.route ? 'white' : 'blue-grey lighten-2'">{{ item.icon }}</v-icon>
            </v-list-tile-action>
            <v-list-tile-title v-text="item.title" class="font-weight-regular"></v-list-tile-title>
          </v-list-tile>
        </template>
      </v-list>

      <v-footer
        class="bg-sidebar-footer"
        height="50"
        fixed
      >
        <v-layout row>
          <v-flex text-xs-center xs3 v-if="!miniVariant">
            <v-icon color="indigo lighten-3" class="sidebar__icon">speaker_notes</v-icon>
          </v-flex>
          <v-flex text-xs-center xs3 v-if="!miniVariant">
            <v-icon color="indigo lighten-3" class="sidebar__icon">settings</v-icon>
          </v-flex>
          <v-flex text-xs-center xs3 v-if="!miniVariant">
            <v-icon color="indigo lighten-3" class="sidebar__icon">today</v-icon>
          </v-flex>
          <v-flex text-xs-center xs3="miniVariant">
            <v-icon color="indigo lighten-3" class="sidebar__icon" @click="logout()">power_settings_new</v-icon>
          </v-flex>
        </v-layout>
      </v-footer>
    </v-navigation-drawer>
  </div>
</template>

<script>
export default {
  name: 'Sidebar',
  props: {
    mini: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      showSidebar: true,
      items: [
        {
          icon: 'dashboard',
          title: 'Dashboard',
          route: '/'
        },
        {
          icon: 'add_shopping_cart',
          title: 'Expenses',
          route: '/expenses'
        },
        {
          icon: 'home',
          title: 'Utilities',
          route: '/utilities'
        },
        {
          icon: 'account_balance_wallet',
          title: 'Debts',
          route: '/debts'
        },
      ]
    }
  },
  computed: {
    miniVariant () {
      return this.mini && this.showSidebar
    }
  },
  methods: {
    openLink (item) {
      
    },
    sidebarMouseEnter () {
      if (this.mini) {
        this.showSidebar = false
      }
    },
    sidebarMouseLeave () {
      if (this.mini) {
        this.showSidebar = true
      }
    },
    logout () {
      this.$store.dispatch('logoutUser')
        .then(() => {
          this.$router.push({ name: 'Login' })
        })
    },
    checkVisibleLinkGroup () {
      this.items = this.items.map(item => {
        if (item.group) {
          let flag = true
          item.list.map((listItem, index) => {
            if (flag && this.$route.path === listItem.route) {
              flag = false
              item.visible = true
            } else if (index === item.list.length - 1 && flag) {
              item.visible = false
            }
          })
        }
        return item
      })
    }
  },
  created () {
    this.checkVisibleLinkGroup()
  }
}
</script>

