  .sidebar-links .v-list__tile__title {
    opacity 1
    transition opacity 0.2s ease 0s
  }

  &.v-navigation-drawer--mini-variant .sidebar-links .v-list__tile__title {
    opacity 0
  }

  .v-footer {
    transition .4s cubic-bezier(.4,0,.2,1)
  }

  .v-icon {
    color inherit!important
  }
