<template>
  <tr>
    <td>
      {{ category.name }}
    </td>
    <td>
      {{ description }}
    </td>
    <td class="price text-right">
      {{ price }} <span v-html="currencySign"></span>
    </td>
    <td>
      <i class="edit-btn fa fa-pencil" @click="editExpenses"></i>
    </td>
  </tr>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'GeneralExpense',
  props: {
    description: {
      type: String
    },
    category: {
      type: Object
    },
    price: {
      type: [Number, String],
      required: true
    },
    date: {
      type: String,
      required: true
    },
    currency: {
      type: String,
      required: true
    }
  },
  computed: {
    ...mapGetters({
      categories: 'getExpenseCategories',
      currencies: 'getCurrencies'
    }),
    currencySign () {
      return this.$store.getters.getCurrencySignByCode(this.currency)
    }
  },
  methods: {
    editExpenses () {
      this.$store.dispatch('editExpensesByDate', this.date)
      window.scrollTo(0, 0)
    }
  }
}
</script>

