tr.padding-td-0 td {
  padding-left: 0;
  padding-right: 0;
}

tr.padding-td-0 td:first-child {
  padding-left: 8px;
  padding-right: 0;
}

button.btn {
  color: #ffffff;
}

tr td:last-child {
  position: relative;
}

.edit-btn {
  transition: opacity 0.1s linear;
  font-size: 16px;
  position: absolute;
  top: 3px;
  cursor: pointer;
  color: rgb(0, 166, 90);
  right: 3px;
  opacity: 0;
}

.edit-btn.delete {
  color: #dd4b39;
