<template>
  <div>
    <div class="overlay" v-if="expense.status.isDeleted && !expense.status.isFailure">
      <button class="btn btn-success btn-xs" @click="restore()">Restore</button>
    </div>
    <div class="overlay" v-if="expense.status.isLoading || expense.status.isSaved || expense.status.isFailure">
      <div class="loading" v-if="expense.status.isLoading"><i class="fa fa-spinner"></i></div>
      <div class="failure" v-if="expense.status.isFailure">
        <i class="fa fa-exclamation-triangle"></i>
        <br>
        <br>
        <button class="btn btn-success btn-xs failure" @click="tryFix()">Edit</button>
      </div>
      <div class="done" v-if="expense.status.isSaved"><i class="fa fa-check"></i></div>
    </div>

    <div style="position: relative;">
      <div class="input-group" :class="{ deleted: expense.status.isDeleted }">
        <div class="input-group-btn" style="width: 100%">
          <category-autocomplete :category-id="expense.category" @select="handleCategorySelect" @clear="clearExpenseCategory"/>
        </div>
        <div class="input-group-addon" @click="deleteExpense()"><i class="fa fa-trash"></i></div>
      </div>
      <div class="second-line">
        <div class="is-general-toggle col"
             :class="{ active: !expense.isGeneral }"
             @click="setExpenseType(false)"
        >
          <i class="fa fa-user"></i>
        </div>
        <div class="is-general-toggle col"
             :class="{ active: expense.isGeneral }"
             @click="setExpenseType(true)"
        >
          <i class="fa fa-users"></i>
        </div>
        <div class="col">
          <div :style="{ backgroundColor: categoryColor + '70' }"></div>
        </div>
        <div class="input-group price-wrap col">
          <input type="number" class="form-control price" v-model="price" placeholder="0">
          <div class="input-group-addon currency">
            <div class="dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
              <span v-html="currencySign(currency.code)"></span>
            </div>
            <ul class="dropdown-menu">
              <li @click="selectCurrency(currency)" v-for="currency in currencies">
                <a href="javascript:void(0);">
                  <span v-html="currencySign(currency.code)"></span>
                </a>
              </li>
            </ul>
          </div>
        </div>
        <div class="clearfix"></div>
      </div>
      <div>
        <input type="text" class="form-control expense-details" :placeholder="t('Description') + '...'" v-model="description">
      </div>
    </div>
  </div>
</template>

<script>
import CategoryAutocomplete from './CategoryAutocomplete'
import { mapGetters, mapActions } from 'vuex'
import * as types from '@/store/mutation-types'
// import $ from 'jquery'

export default {
  name: 'ExpenseEdit',
  components: {
    CategoryAutocomplete
  },
  props: {
    uuid: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      showCurrencyList: false
    }
  },
  computed: {
    ...mapGetters({
      expenseCategories: 'getExpenseCategories',
      currencies: 'getCurrencies',
      currencySign: 'getCurrencySignByCode'
    }),
    expense () {
      return this.$store.getters.getEditExpenseByUUID(this.uuid)
    },
    price: {
      get () {
        return this.expense.price
      },
      set (value) {
        this.update('price', value)
      }
    },
    description: {
      get () {
        return this.expense.description
      },
      set (value) {
        this.update('description', value)
      }
    },
    category () {
      return this.$store.getters.getExpenseCategoryById(this.expense.category)
    },
    categoryColor () {
      return this.category ? this.category.color : '#ffffff'
    },
    currency () {
      return this.$store.getters.getCurrencyById(this.expense.currency)
    }
  },
  methods: {
    ...mapActions([
      'updateEditExpenseFieldByUUID'
    ]),
    update (field, value) {
      this.$store.dispatch('updateEditExpenseFieldByUUID', {
        expenseUUID: this.uuid,
        expenseUpdatedField: {
          [field]: value
        }
      })
    },
    deleteExpense () {
      this.$store.dispatch('deleteEditExpenseByUUID', this.uuid)
    },
    handleCategorySelect (categoryId) {
      this.update('category', categoryId)
    },
    selectCurrency (currency) {
      this.update('currency', currency.id)
    },
    clearExpenseCategory () {
      this.update('category', null)
    },
    setExpenseType (value) {
      this.update('isGeneral', value)
    },
    restore () {
      this.$store.commit(types.CLEAR_EDIT_EXPENSE_DELETED_STATUS, this.uuid)
    },
    tryFix () {
      this.$store.commit(types.CLEAR_EDIT_EXPENSE_FAILURE_STATUS, this.uuid)
    }
  },
  mounted () {
    // $('.expenses')
    //   .find('input')
    //   .attr('autocorrect', 'off')
    //   .attr('autocapitalize', 'off')
    //   .attr('spellcheck', 'false')
  }
}
</script>

