import Vue from 'vue'
import * as types from '../mutation-types'
import ExpensesApi from '@/services/expenses'
import { generateUUID, getDateWithoutTime } from '@/utils/helpers'

const state = {
  date: getDateWithoutTime().format(),
  expenses: {},
  expenseList: []
}

const getters = {
  getEditExpenseByUUID: state => expenseUUID => state.expenses[expenseUUID],
  isAllSavedEditExpenses: state => {
    let expenseListLength = state.expenseList.length
    if (expenseListLength === 0) {
      return false
    }
    state.expenseList.forEach(uuid => {
      if (state.expenses[uuid].status.isSaved) {
        expenseListLength--
      }
    })
    return !expenseListLength
  }
}

const actions = {
  addNewEditExpense ({ state, commit, getters }, isGeneral) {
    commit(types.SET_EDIT_EXPENSE, {
      isGeneral: isGeneral || true,
      currency: getters.getFirstCurrency.id,
      price: '',
      description: '',
      category: getters.getFirstExpenseCategory.id
    })
  },
  updateEditExpenseFieldByUUID ({ state, commit }, payload) {
    commit(types.SET_EDIT_EXPENSE_UPDATED_STATUS, payload.expenseUUID)
    commit(types.UPDATE_EDIT_EXPENSE, {
      expenseUUID: payload.expenseUUID,
      expenseUpdatedField: payload.expenseUpdatedField
    })
  },
  deleteEditExpenseByUUID ({ state, commit }, expenseUUID) {
    if (state.expenses[expenseUUID].id) {
      commit(types.SET_EDIT_EXPENSE_DELETED_STATUS, expenseUUID)
    } else {
      commit(types.DELETE_EDIT_EXPENSE, expenseUUID)
    }
  },
  saveEditExpenses ({ state, commit }) {
    state.expenseList.forEach(expenseUUID => {
      commit(types.SET_EDIT_EXPENSE_LOADING_STATUS, expenseUUID)
      let expense = {...state.expenses[expenseUUID]}
      expense['date'] = getDateWithoutTime(state.date).format()
      if (expense.status.isDeleted && expense.id) {
        ExpensesApi.delete(expense.id)
          .then(() => {
            commit(types.DELETE_EXPENSE, expense.id)
            commit(types.SET_EDIT_EXPENSE_SAVED_STATUS, expenseUUID)
          })
          .catch(() => {
            commit(types.SET_EDIT_EXPENSE_FAILURE_STATUS, expenseUUID)
          })
      }
      if (expense.status.isUpdated) {
        if (!expense.id) {
          ExpensesApi.store(expense)
            .then(id => {
              expense['id'] = id
              commit(types.SET_EXPENSE, expense)
              commit(types.UPDATE_EDIT_EXPENSE, expense)
              commit(types.SET_EDIT_EXPENSE_SAVED_STATUS, expenseUUID)
            })
            .catch(() => {
              commit(types.SET_EDIT_EXPENSE_FAILURE_STATUS, expenseUUID)
            })
        } else {
          ExpensesApi.update(expense.id, expense)
            .then(() => {
              commit(types.UPDATE_EXPENSE, expense)
              commit(types.SET_EDIT_EXPENSE_SAVED_STATUS, expenseUUID)
            })
            .catch(() => {
              commit(types.SET_EDIT_EXPENSE_FAILURE_STATUS, expenseUUID)
            })
        }
      } else {
        commit(types.SET_EDIT_EXPENSE_SAVED_STATUS, expenseUUID)
      }
      commit(types.CLEAR_EDIT_EXPENSE_LOADING_STATUS, expenseUUID)
    })
  },
  updateEditExpenseDate ({ state, commit }, date) {
    state.expenseList.forEach(uuid => {
      if (!state.expenses[uuid].status.isDeleted) {
        commit(types.SET_EDIT_EXPENSE_UPDATED_STATUS, uuid)
      }
    })
    commit(types.SET_EDIT_EXPENSE_DATE, date)
  }
}

const mutations = {
  [types.SET_EDIT_EXPENSE] (state, expense) {
    let expenseUUID = generateUUID()
    Vue.set(expense, 'status', {
      isLoading: false,
      isUpdated: false,
      isSaved: false,
      isDeleted: false,
      isFailure: false
    })
    Vue.set(state.expenses, expenseUUID, expense)
    state.expenseList.push(expenseUUID)
  },
  [types.UPDATE_EDIT_EXPENSE] (state, payload) {
    Vue.set(state.expenses, payload.expenseUUID, {...state.expenses[payload.expenseUUID], ...payload.expenseUpdatedField})
  },
  [types.DELETE_EDIT_EXPENSE] (state, expenseUUID) {
    Vue.delete(state.expenses, expenseUUID)
    state.expenseList.forEach((item, index) => {
      if (item === expenseUUID) {
        state.expenseList.splice(index, 1)
      }
    })
  },
  [types.CLEAR_EDIT_EXPENSES] (state) {
    state.expenses = {}
    state.expenseList = []
  },
  [types.SET_EDIT_EXPENSE_UPDATED_STATUS] (state, expenseUUID) {
    resetStatus(state.expenses[expenseUUID].status)
    state.expenses[expenseUUID].status.isUpdated = true
  },
  [types.SET_EDIT_EXPENSE_LOADING_STATUS] (state, expenseUUID) {
    state.expenses[expenseUUID].status.isLoading = true
  },
  [types.CLEAR_EDIT_EXPENSE_LOADING_STATUS] (state, expenseUUID) {
    state.expenses[expenseUUID].status.isLoading = false
  },
  [types.SET_EDIT_EXPENSE_FAILURE_STATUS] (state, expenseUUID) {
    state.expenses[expenseUUID].status.isFailure = true
  },
  [types.CLEAR_EDIT_EXPENSE_FAILURE_STATUS] (state, expenseUUID) {
    state.expenses[expenseUUID].status.isFailure = false
  },
  [types.SET_EDIT_EXPENSE_SAVED_STATUS] (state, expenseUUID) {
    resetStatus(state.expenses[expenseUUID].status)
    state.expenses[expenseUUID].status.isSaved = true
  },
  [types.SET_EDIT_EXPENSE_DELETED_STATUS] (state, expenseUUID) {
    state.expenses[expenseUUID].status.isDeleted = true
  },
  [types.CLEAR_EDIT_EXPENSE_DELETED_STATUS] (state, expenseUUID) {
    state.expenses[expenseUUID].status.isDeleted = false
  },
  [types.SET_EDIT_EXPENSE_DATE] (state, date) {
    state.date = date
  }
}

function resetStatus (status) {
  status.isUpdated = false
  status.isSaved = false
  status.isFailure = false
  status.isLoading = false
  status.isDeleted = false
}

export default {
  state,
  mutations,
  actions,
  getters
}
