import * as types from '../mutation-types'
import Auth from '@/services/auth'

const state = {
  name: 'John Doe',
  isLoggedIn: Auth.checkAuth()
}

const getters = {
}

const actions = {
  fetchUser ({ commit }) {
    return Auth.user()
      .then((user) => {
        commit(types.SET_AUTH_USER, user)
      })
      .catch(() => {
        console.warn('User load error. Please reload page!')
      })
  },
  loginUser ({ commit }, credentials) {
    return Auth.login(credentials.email, credentials.password)
      .then(() => {
        commit(types.LOGIN_USER)
      })
      .catch(() => {
      })
  },
  logoutUser ({ commit }) {
    if (Auth.checkAuth()) {
      return Auth.logout()
        .then(() => {
          commit(types.LOGOUT_USER)
        })
        .catch(() => {
        })
    } else {
      commit(types.LOGOUT_USER)
    }
    return true
  }
}

const mutations = {
  [types.SET_AUTH_USER] (state, user) {
    state.name = user.name
  },
  [types.LOGIN_USER] (state) {
    state.isLoggedIn = true
  },
  [types.LOGOUT_USER] (state) {
    state.isLoggedIn = false
  }
}

export default {
  state,
  mutations,
  getters,
  actions
}
