import axios from 'axios'

const USER_URL = '/auth/user'
const LOGIN_URL = '/auth/login'
const LOGOUT_URL = '/auth/logout'
const REFRESH_URL = '/auth/refresh'

export default {
  user () {
    return axios.get(USER_URL)
  },
  login (email, password) {
    return new Promise((resolve, reject) => {
      axios
        .post(LOGIN_URL, {
          email,
          password
        })
        .then(data => {
          this.setJwtAuth(data)
          resolve(true)
        })
        .catch(error => reject(error.response))
    })
  },
  logout () {
    return new Promise((resolve, reject) => {
      axios
        .post(LOGOUT_URL)
        .then(() => {
          this.clearJwtAuth()
          resolve(true)
        })
        .catch(error => reject(error.response))
    })
  },
  refreshToken () {
    return new Promise((resolve, reject) => {
      axios
        .post(REFRESH_URL)
        .then(data => {
          this.setJwtAuth(data)
          resolve(true)
        })
        .catch(error => reject(error.response))
    })
  },
  checkAuth () {
    const jwtAuth = this.getJwtAuth()
    return !!jwtAuth && !!jwtAuth.accessToken
  },
  getAuthHeader () {
    const jwtAuth = this.getJwtAuth()
    if (jwtAuth) {
      return 'Bearer ' + jwtAuth.accessToken
    }
    return ''
  },
  setJwtAuth (jwtAuth) {
    localStorage.setItem('jwt_auth', JSON.stringify(jwtAuth))
  },
  getJwtAuth () {
    return JSON.parse(localStorage.getItem('jwt_auth'))
  },
  clearJwtAuth () {
    localStorage.removeItem('jwt_auth')
  }
}
