import axios from 'axios'

const UTILITIES_URL_PATH = '/utility-indications'

export default {
  getAll () {
    return axios.get(UTILITIES_URL_PATH)
  },
  store (params) {
    return axios.post(UTILITIES_URL_PATH, params)
  },
  destroy (id) {
    return axios.delete(UTILITIES_URL_PATH + `/${id}`)
  }
}
