import * as types from '../mutation-types'
import CurrenciesAPI from '@/services/currencies'

const state = {
  currencySign: {
    'UAH': '&#8372;',
    'USD': '&#036;',
    'EUR': '&#128;'
  },
  list: []
}

const getters = {
  getCurrencies: state => state.currencies,
  getCurrencyById: state => id => state.currencies.find(currency => currency.id === id),
  getCurrencyByCode: state => code => state.currencies.find(currency => currency.code === code),
  getFirstCurrency: state => state.currencies[0],
  getCurrencySignByCode: state => code => state.currencySign[code],
  getCurrencySignByID: (state, getters) => id => {
    let code = getters.getFirstCurrency.code
    if (id && !isNaN(id)) {
      code = getters.getCurrencyById(id).code
    }
    return state.currencySign[code]
  }
}

const actions = {
  getCurrencies ({ state, commit }) {
    return CurrenciesAPI.getAll()
      .then(currencies => {
        commit(types.SET_CURRENCIES, currencies)
      })
      .catch(error => {
        console.error(error)
      })
  }
}

const mutations = {
  [types.SET_CURRENCIES] (state, currencies) {
    state.list = []
    currencies.forEach(({ id, name, code }) => {
      state.list.push({
        id,
        name,
        code
      })
    })
  }
}

export default {
  state,
  mutations,
  actions,
  getters
}
