<template>
  <div v-if="!isLoading">
    <div style="margin-bottom: 10px;" class="hidden-xs">
      <button class="btn btn-sm btn-info btn-flat pull-left"
              v-for="month in months"
              @click="getExpensesByMonthNumber(month.number)"
              :class="{ active: month.number === activeMonth }"
              v-text="month.name"
      ></button>
      <div class="clearfix"></div>
    </div>
    <div class="row">
      <div class="col-md-6">
        <div class="box" style="border-top-color: #00a1ff;">
          <div class="box-body" style="overflow: visible; max-height: none;">
            <expenses-edit-form v-if="!isMobile" style="padding: 15px 0;"/>
            <expenses-edit-form-mobile v-else style="padding: 5px;"/>
          </div>
        </div>
      </div>
      <div class="col-md-6 hidden-xs">
        <div class="row">
          <div class="col-md-6">
            <amount-expenses icon="ion-pizza" type="food" limit="2500" color="#ffffff" background-color="#00c0ef"/>
          </div>
          <div class="col-md-6">
            <amount-expenses icon="ion-ios-person" type="personal" :limit="personalLimitStat" color="#ffffff" background-color="#00a65a"/>
          </div>
        </div>
        <div class="row">
          <div class="col-md-6">
            <amount-expenses icon="ion-ios-people" type="general" limit="5000" color="#ffffff" background-color="#00a65a"/>
          </div>
          <div class="col-md-6">
            <expense-categories/>
          </div>
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col-md-9">
        <!-- TABLE: FOOD EXPENSES -->
        <food-expenses-table/>
        <!-- /.box -->
        <div class="row">
          <div class="col-md-6">
            <!-- TABLE: GENERAL EXPENSES -->
            <expenses-table :is-general="true"/>
            <!-- /.box -->
          </div>
          <div class="col-md-6">
            <!-- TABLE: PERSONAL EXPENSES -->
            <expenses-table :is-general="false"/>
            <!-- /.box -->
          </div>
        </div>
      </div>
      <div class="col-md-3">
        <!-- TABLE: EXPENSE TYPES -->
        <expense-categories-table/>
        <!-- /.box -->
      </div>
    </div>
  </div>
</template>

<script>
import ExpensesTable from '@/views/expenses/ExpensesTable'
import FoodExpensesTable from '@/views/expenses/FoodExpensesTable'
import ExpenseCategoriesTable from '@/views/expenses/ExpenseCategoriesTable'
import AmountExpenses from '@/components/statistics/AmountExpenses'
import ExpenseCategories from '@/components/statistics/ExpenseCategories'
import ExpensesEditForm from '@/views/expenses/partials/ExpensesEditForm'
import ExpensesEditFormMobile from '@/views/expenses/partials/mobile/ExpensesEditFormMobile'

export default {
  name: 'Expenses',
  components: {
    ExpensesTable,
    FoodExpensesTable,
    ExpenseCategoriesTable,
    AmountExpenses,
    ExpensesEditForm,
    ExpensesEditFormMobile,
    ExpenseCategories
  },
  data () {
    return {
      isMobile: false,
      isLoading: true,
      activeMonth: '',
      months: [
        {
          name: 'Жовтень 2017',
          number: 10
        },
        {
          name: 'Листопад 2017',
          number: 11
        },
        {
          name: 'Грудень 2017',
          number: 12
        },
        {
          name: 'Сiчень',
          number: 1
        },
        {
          name: 'Лютий',
          number: 2
        },
        {
          name: 'Березень',
          number: 3
        },
        {
          name: 'Квiтень',
          number: 4
        },
        {
          name: 'Травень',
          number: 5
        },
        {
          name: 'Червень',
          number: 6
        },
        {
          name: 'Липень',
          number: 7
        },
        {
          name: 'Серпень',
          number: 8
        }
      ]
    }
  },
  computed: {
    personalLimitStat () {
      return (this.$store.getters.currentUser.name === 'Vadim Osochenko') ? 2500 : 1300
    }
  },
  methods: {
    getExpensesByMonthNumber (monthNumber) {
      this.activeMonth = monthNumber
      this.$bus.$emit('start-loading')
      this.$store.dispatch('getExpensesByMonthNumber', monthNumber).then(() => {
        this.$bus.$emit('stop-loading')
      })
    }
  },
  created () {
    let currentMonthNumber = (new Date()).getMonth() + 1
    this.activeMonth = currentMonthNumber
    Promise
      .all([
        this.$store.dispatch('getExpenseCategories'),
        this.$store.dispatch('getCurrencies'),
        this.$store.dispatch('getExpensesByMonthNumber', currentMonthNumber)
      ])
      .then(() => {
        this.isLoading = false
      })
    this.isMobile = document.body.clientWidth < 768
  }
}
</script>

<style scoped>
.btn-info.active {
  background-color: #0097c3;
}
</style>