<template>
  <p>
    {{ name }}: {{ amount }}{{ unit }}
  </p>
</template>

<script>
export default {
  name: 'UtilityForText',
  props: {
    id: {
      type: Number,
      required: true
    },
    name: {
      type: String,
      required: true
    },
    unit: {
      type: String,
      required: true
    },
    date: {
      type: String,
      required: true
    }
  },
  computed: {
    amount () {
      return this.$store.getters.getUtilityAmountByTypeAndDate({
        'type': this.id,
        'date': this.date
      })
    }
  }
}
</script>

<style scoped>
.label {
  margin: 0 2px;
  font-size: 11px;
}
</style>