import * as types from '../mutation-types'
import ExpenseCategoryApi from '@/services/expenseCategories'

const INIT_COLOR = '#E0D5D5'

const state = {
  id: '',
  isEdible: false,
  name: '',
  color: INIT_COLOR,
  status: {
    isFailure: false,
    isLoading: false,
    isSaved: false,
    tryDelete: false
  },
  visible: false
}

const actions = {
  setEditExpenseCategory ({ commit, getters }, id) {
    if (id) {
      commit(types.INIT_EDIT_EXPENSE_CATEGORY, getters.getExpenseCategoryById(id))
    } else {
      commit(types.INIT_EDIT_EXPENSE_CATEGORY, {})
    }
  },
  addExpenseCategory ({ state, commit }) {
    commit(types.SET_EXPENSE_CATEGORY_LOADING_STATUS)
    let expenseCategory = {
      isEdible: !!state.isEdible,
      name: state.name,
      color: state.color
    }
    ExpenseCategoryApi.store(expenseCategory)
      .then(id => {
        expenseCategory['id'] = id
        commit(types.SET_EXPENSE_CATEGORY, expenseCategory)
        commit(types.SET_EXPENSE_CATEGORY_SAVED_STATUS)
      })
      .catch(error => {
        console.log(error)
        commit(types.SET_EXPENSE_CATEGORY_FAILURE_STATUS)
      })
  },
  updateExpenseCategory ({ commit }, id) {
    commit(types.SET_EXPENSE_CATEGORY_LOADING_STATUS)
    let expenseCategory = {
      id: id,
      isEdible: !!state.isEdible,
      name: state.name,
      color: state.color
    }
    ExpenseCategoryApi.update(id, expenseCategory)
      .then(() => {
        commit(types.UPDATE_EXPENSE_CATEGORY, expenseCategory)
        commit(types.SET_EXPENSE_CATEGORY_SAVED_STATUS)
      })
      .catch(error => {
        console.log(error)
        commit(types.SET_EXPENSE_CATEGORY_FAILURE_STATUS)
      })
  },
  deleteExpenseCategory ({ commit }, id) {
    commit(types.SET_EXPENSE_CATEGORY_LOADING_STATUS)
    ExpenseCategoryApi.delete(id)
      .then(() => {
        commit(types.DELETE_EXPENSE_CATEGORY, id)
        commit(types.SET_EXPENSE_CATEGORY_SAVED_STATUS)
      })
      .catch(error => {
        console.log(error)
        commit(types.SET_EXPENSE_CATEGORY_FAILURE_STATUS)
      })
  }
}

const mutations = {
  [types.INIT_EDIT_EXPENSE_CATEGORY] (state, expenseCategory) {
    state.id = expenseCategory.id || ''
    state.isEdible = !!expenseCategory.isEdible || ''
    state.name = expenseCategory.name || ''
    state.color = expenseCategory.color || INIT_COLOR
    resetStatus(state)
  },
  [types.CLEAR_EXPENSE_CATEGORY] (state) {
    resetStatus(state)
    state.id = ''
    state.isEdible = false
    state.name = ''
    state.color = INIT_COLOR
    state.visible = false
  },
  [types.UPDATE_EXPENSE_CATEGORY_NAME] (state, value) {
    state.name = value
  },
  [types.UPDATE_EXPENSE_CATEGORY_IS_EDIBLE] (state, value) {
    state.isEdible = value
  },
  [types.UPDATE_EXPENSE_CATEGORY_COLOR] (state, value) {
    state.color = value
  },
  [types.SET_EXPENSE_CATEGORY_LOADING_STATUS] (state) {
    resetStatus(state)
    state.status.isLoading = true
  },
  [types.SET_EXPENSE_CATEGORY_FAILURE_STATUS] (state) {
    resetStatus(state)
    state.status.isFailure = true
  },
  [types.SET_EXPENSE_CATEGORY_SAVED_STATUS] (state) {
    resetStatus(state)
    state.status.isSaved = true
  },
  [types.SET_EXPENSE_CATEGORY_TRY_DELETE_STATUS] (state) {
    resetStatus(state)
    state.status.tryDelete = true
  },
  [types.SET_EXPENSE_CATEGORY_VISIBLE] (state, value) {
    state.visible = value
  }
}

function resetStatus (state) {
  state.status.isSaved = false
  state.status.isFailure = false
  state.status.isLoading = false
  state.status.tryDelete = false
}

export default {
  state,
  mutations,
  actions
}
