<template>
  <div class="table">
    <table class="table no-margin table-hover">
      <thead>
      <tr>
        <th class="col-xs-3 col-sm-4">Послуги</th>
        <th class="col-xs-5 col-sm-2">За мiсяць</th>
        <th class="col-sm-2 hidden-xs">Показання</th>
        <th class="col-sm-4 col-sm-1">Сума</th>
        <th class="col-sm-1 hidden-xs">Валюта</th>
      </tr>
      </thead>
      <tbody>
        <Utility v-for="utilityType in utilityTypes"
                 :key="utilityType.id"
                 :id="utilityType.id"
                 :name="utilityType.name"
                 :rate-rule-ids="utilityType.rateRules"
                 :unit="utilityType.unit"
                 :currency-id="utilityType.currency"
        />
      </tbody>
    </table>
    <table class="table no-margin">
      <thead>
      <tr>
        <th class="col-xs-3 col-sm-9"></th>
        <th class="col-xs-9 col-sm-3"></th>
      </tr>
      </thead>
      <tbody>
      <tr>
        <td></td>
        <td class="text-right">
          <table class="table no-margin text-center">
            <thead>
            <tr>
              <th class="col-xs-7 col-sm-2">Мiсяць</th>
              <th class="col-xs-5 col-sm-2">Пiдсумок</th>
            </tr>
            </thead>
            <tbody>
            <tr style="font-size: 18px; font-weight: 600; font-style: italic;">
              <td>{{ monthName }}</td>
              <td>{{ monthTotalAmount }} грн</td>
            </tr>
            </tbody>
          </table>
        </td>
      </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { months } from '@/utils/helpers'
import UtilityForTable from '@/views/utilities/UtilityForTable'

export default {
  name: 'ActiveMonthUtility',
  components: {
    Utility: UtilityForTable
  },
  computed: {
    ...mapGetters({
      utilityTypes: 'getCreatedUtilityTypes',
      date: 'getUtilitiesDate'
    }),
    monthTotalAmount () {
      return this.$store.getters.getMonthUtilitiesTotalByDate(this.date)
    },
    monthName () {
      return months[this.$moment(this.date).month()].name
    }
  }
}
</script>